import React, {useRef, useState} from 'react';
import Others from "../layout/Others";
import {sendVerification} from "../../Services/apis";
import {toast} from "react-toastify";
import Auth from "../../Models/http/Auth";

export default function RecoverPassword({ history}) {
    const [sending, setSending] = useState(false);
    const [email, setEmail] = useState('');

    const sendSMS = async (email) => {
        if (email !== '') {
            setSending(true);
            await Auth.verifyResetPasswordEmail(email).then(response => {
                setEmail(email);
                setSending(false);
                toast.success('Reset link sent to your email');
            }).catch(error => {
                console.log(error.response.data.message);
                setSending(false);
                toast.error(error.response.data.message);
            });
        }
    }


    return (
        <Others>
            <div className="forgot-password-page">
                <div className="grid">
                    <div className="forgot-password-holder">
                        <div className="block-title">
                            <img src="/img/arrow-down.png" alt="" className="title-icon" />
                            <span>Forgot Password?</span>
                        </div>
                        <div className="forgot-password-content">
                            <p>Enter your registered email to reset your password </p>
                            <div className="form-holder">
                                <div className="input-group">
                                    <p className="label">Email address:</p>
                                    <input type="email" name="email" value={email} onChange={(e) => setEmail(e.target.value)} />
                                    {/* <p className="aqx-loax-a-info">
                                        <i className="fa fa-info-circle aqx-loax-a-info-ico" /> Do not include 0 when
                                        entering your number; start with 7 or 1</p> */}
                                </div>
                                <div className="aqx-loax-b">
                                    <button
                                        className="aqx-loax-btn"
                                        onClick={() => sendSMS(email)}
                                        disabled={sending}
                                    >Send link {sending && <i className="fa fa-spin fa-spinner" /> }</button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </Others>
    )
}
