import React, {useState, useEffect} from "react";
import { withRouter } from 'react-router-dom';
import {createStructuredSelector} from "reselect";
import { connect } from "react-redux";
import {
    selectRematchGroups,
    selectRematchLeagues,
    selectRematchSportsBook
} from "../../Redux/custom/sports/rematch/rematchSports.selectors";
import RematchSportsBook from "../../Models/http/RematchSportsBook";
import './rematch-tablet-component.css';
import Loader from "./Loader";
import FixturesList from "./FixturesList";
import EventFixturesList from "./event-fixtures-list";
import {
    selectAntePostGroups,
    selectAntePostSportsBook
} from "../../Redux/custom/sports/antepost/antePostSports.selectors";
import AntePostSportsBook from "../../Models/http/AntePostSportsBook";
import AntePostEventFixturesList from "./antepost-event-fixtures-list";

const AntePostComponentTablet = ({ match, antePostGroups, antePostSports }) => {
    const [loading, setLoading] = useState(true);
    const [league, setLeague] = useState([]);
    const [group, setGroup] = useState([]);
    const [sport, setSport] = useState([]);
    const [events, setEvents] = useState([]);
    const [oddsStructure, setOddsStructure] = useState([]);

    const [oddsGroup, setOddsGroup] = useState([]);

    const [activeGroup, setActiveGroup] = useState(0);

    const [activeStructure, setActiveStructure] = useState([]);
    const [activeGroups, setActiveGroups] = useState([]);
    const [activeSigns, setActiveSigns] = useState([]);


    const fetchGroupInformation = async () =>{
        // Find league information
        await antePostGroups.forEach(group => {
            if (parseInt(group.group_id) === parseInt(match.params.group_id)){
                setGroup(group);
                // Find Group Information
                antePostSports.forEach(spor => {
                    if (parseInt(spor.sport_id) === parseInt(group.sport_id)){
                        setSport(spor)
                    }
                })

            }
        });
    }

    const fetchGroupEvents = async () => {
        AntePostSportsBook.fetchAntePostGroupEvents(match.params.group_id).then(response => {
            setEvents(response.data.data);
            console.log(response.data.data)
            // setOddsStructure(response.data.data.oddstructure);
            // const str = [];
            // response.data.data.oddstructure.forEach(structure => {
            //     str.push(...structure.groups);
            // })
            // setOddsGroup(str);

            setLoading(false);
        }).catch(error => {
            setLoading(false);
            console.log(error.response);
        })
    }


    useEffect( () => {
        setLoading(true);
        if (antePostGroups){
            async function fetchData() {
               await fetchGroupInformation();
            }
            fetchData().then(async res =>{
                await fetchGroupEvents();
            });
        }
    }, [antePostGroups, antePostSports, match.params.group_id]);

   const setDefaultStructure = () =>{
        if (activeGroups.length < 1 && oddsStructure.length > 0){
            console.log('got me ooo');
            oddsStructure.forEach(odds => {
                if (odds.button_name === 'Main'){
                    setActiveStructure(odds);
                    setActiveGroups(odds.groups)

                    // Set the active group
                    odds.groups.forEach(group => {
                        if (group.group_name === "1X2"){
                            setActiveGroup(group);
                            setActiveSigns(group.signs)
                        }
                    });
                }
            });
        }

       return oddsStructure?.map((odds, g) => <li
               key={`group-${odds.button_name}`}
               onClick={() => {
                   setActiveStructure(odds);
                   setActiveGroups(odds.groups)}}
               className={`itm${g+1} ${odds.button_name === activeStructure.button_name ? 'active-group' : ''}`}
           >
               <span>{odds.button_name.substring(0,10)}</span>
           </li>
       )
    }

   const setEventsTablet = () => {
       if (loading){
           return <Loader />
       }else if (!loading){
           return <div>
               <div className="custom-odds-table">
                   <ul className="custom-groups">
                       {activeGroups?.map((group, g) =>
                           <li
                               key={`group-${group.group_name}`}
                               onClick={() => {
                                   setActiveGroup(group);
                                   setActiveSigns(group.signs)
                               }}
                               className={`itm${g+1} ${group.group_name === activeGroup.group_name ? 'active-group' : ''}`}
                           >
                               <span>{group.group_name.substring(0,10)}</span>
                           </li>
                       )}
                   </ul>
               </div>

               <AntePostEventFixturesList signs={activeSigns} activeGroup={activeGroup} leagueGroup={group} league={league} events={events}  />
           </div>
       }else {
           return <div className="custom-odds-table">
               <h1 style={{ textAlign: "center" }}>No event for this league</h1>
           </div>
       }

   }

    return (
        <div className="oddsViewPanel">
            <div className={`divOdds r1 c`}>
                <div className="title">
                    <span>
                        <span>{sport?sport.name:null}</span>
                        <span>{group?group.name:null}</span>
                    </span>
                    <div className="btns">
                        <a className="lnkOddsCls" onClick={() => alert('remove')} />
                        <a className="lnkOddsPrn" />
                        <a className="lnkOddsRfh" />
                        <a className="lnkOddsBack" />
                    </div>
                </div>
                <div className="OddsBreadbrum">
                    <ul>
                        <li className="sportItem">
                            <a title="Soccer">{sport?sport.name:null} </a>
                        </li>
                        <li className="groupItem">
                            <a>{group?group.name:null}</a>
                        </li>
                    </ul>
                </div>
            </div>

            <div>

                {
                   setEventsTablet()
                }



            </div>



        </div>
    )
}
const mapStateToProps = createStructuredSelector({
    antePostSports: selectAntePostSportsBook,
    antePostGroups: selectAntePostGroups,
});
export default withRouter(connect(mapStateToProps)(AntePostComponentTablet));