import React, {useState, useEffect} from 'react';
import {formatDate, getOdds, isSelected, toggleExtraMarket} from "../../Utils/helpers";
import {createID} from "../../Utils/couponHelpers";
import {addToCoupon} from "../../Redux/actions";
import AntePostSportsBook from "../../Models/http/AntePostSportsBook";

const AntePostEventFixturesList = ({ signs, activeGroup, leagueGroup, league, events }) => {
    const [activeOdds, setActiveOdds] = useState([]);
    const [activeSign, setActiveSign] = useState([]);
    const [selectedItems, setSelectedItems] = useState([]);

    useEffect(() => {
        // Check the event odds that has the same signKey as the active sign sign key
        if (events){

        }
    }, [signs, events]);

    const addOddToBestSlip = (betSlip, odd, event) => {
        // Find if league exist in betslip
        const existingBetSlipLeague = betSlip.find(
            betSlipLeague => betSlipLeague.league === league.name
        );


        // If league does not exist on the betslip
        let match = {
            match: event,
            bets: odd
        }
        let item = {
            leagueGroup: leagueGroup.name,
            league: league.name,
            events: [
               match
            ]
        }
        // Check if the league exists
        return [ ...betSlip, item ]
    }


    const selectOdd = async (betSlip, odd, event) => {
        await setSelectedItems(addOddToBestSlip(betSlip, odd, event));
        console.log(selectedItems)
    }

    const fetchEventOdds = async (search_code) => {
        AntePostSportsBook.fetchAntePostEventOdds(search_code).then(response => {
            console.log(response.data.data)
        }).catch(error => {
            console.log(error);
        })
    }


    const setEventActiveOdds = (odds, event) => {
        let matchOdds = [];
        odds?.forEach(odd => {
            if (odd.group_name == activeGroup.group_name){
                matchOdds.push(odd);
            }
        });
        return matchOdds?.map((odd, index) =>
            index < 9 && (
                <li onClick={() => selectOdd(selectedItems, odd, event)}>
                    <p>{odd.rank}</p>
                    <p className='sign-name'>{odd.sign_name}</p>
                </li>
            )
        )
    }

    return (
        <div className='fixtures-container'>
            <h5>Events</h5>
            <div className="custom-odds-table">
                <ul className='custom-signs' style={{ textAlign: "right" }}>
                    {signs?.map((sign, g) =>
                        <li
                            onClick={() => {
                                setActiveSign(sign);
                            }}
                        >
                        <span>{sign.sign_name}</span>
                        </li>
                    )}
                </ul>
            </div>
            {events?.map((event, index) =>
                <div className='custom-events-list'>
                    <div className='left-side-events-box'>
                        <span>{event.name}</span>
                        <p>{event.startdate}</p>
                    </div>
                    <div className='right-side-events-box'>
                        <ul>
                            {setEventActiveOdds(event.odds, event)}
                        </ul>
                    </div>

                </div>
            )}


        </div>
    )
}

export default AntePostEventFixturesList;