import React, {useRef, useState, useEffect} from 'react';
import Others from "../layout/Others";
import {toast} from "react-toastify";
import Auth from "../../Models/http/Auth";

export default function VerifyEmail({ history, match }) {
    const [verifying, setVerifying] = useState(false);
    const [verifyingDone, setVerifyingDone] = useState(false);
    const [message, setMessage] = useState(null);

    useEffect(() => {
        setVerifying(true);
        setVerifyingDone(false);
        Auth.verifyAccount(match.params.token).then(response => {
            setVerifying(false);
            setVerifyingDone(true);
            setMessage('Email verified, proceed to login.');
            toast.success('Email verified, proceed to login.');
        }).catch(error => {
            setVerifying(false);
            setVerifyingDone(true);
            console.log(error.response.data);
            setMessage(error.response.data.message)
        });
    }, [])




    return (
        <Others>
            <div className="forgot-password-page">
                <div className="grid">
                    <div className="forgot-password-holder">
                        <div className="block-title">
                            <img src="/img/arrow-down.png" alt="" className="title-icon" />
                            <span>Forgot Password?</span>
                        </div>
                        <div className="forgot-password-content">
                            {
                                (verifying)?  <h3 style={{ textAlign: "center" }}>Verifying your email  <i style={{ textAlign: "center" }} className="fa fa-spin fa-spinner" /></h3> : null
                            }

                            {
                                (message)?  <h3 style={{ textAlign: "center" }}>{message}</h3>:null
                            }

                            <div className="form-holder">
                                <div className="input-group">

                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </Others>
    )
}
