import { AntePostSportsActionTypes } from "./antePostSports.types";
import AntePostSportsBook from "../../../../Models/http/AntePostSportsBook";

export const fetchAntePostSportsStart = () => ({
   type: AntePostSportsActionTypes.FETCH_ANTEPOST_SPORTS_START,
});

export const fetchAntePostSportsSuccess = sportsBook => ({
   type: AntePostSportsActionTypes.FETCH_ANTEPOST_SPORTS_SUCCESS,
   payload: sportsBook
});

export const fetchAntePostSportsFailure = error => ({
   type: AntePostSportsActionTypes.FETCH_ANTEPOST_SPORTS_FAILURE,
   payload: error
});

/*
   | Functions that implements the action dispatchers
 */

export const fetchAntePostSportsStartAsync = () => {
   return dispatch => {
      // Dispatch start event
      dispatch(fetchAntePostSportsStart());
      AntePostSportsBook.fetchAntePostSports().then(response => {
         // Dispatch success event
         dispatch(fetchAntePostSportsSuccess(response.data));
      }).catch(error => {
         // Dispatch failure event
         dispatch(fetchAntePostSportsFailure(error.response));
      });
   }
}

