import { SpecialSportsActionTypes } from "./specialSports.types";
import SpecialSportsBook from "../../../../Models/http/SpecialSportsBook";

export const fetchSpecialSportsStart = () => ({
   type: SpecialSportsActionTypes.FETCH_SPECIAL_SPORTS_START,
});

export const fetchSpecialSportsSuccess = sportsBook => ({
   type: SpecialSportsActionTypes.FETCH_SPECIAL_SPORTS_SUCCESS,
   payload: sportsBook
});

export const fetchSpecialSportsFailure = error => ({
   type: SpecialSportsActionTypes.FETCH_SPECIAL_SPORTS_FAILURE,
   payload: error
});

/*
   | Functions that implements the action dispatchers
 */

export const fetchSpecialSportsStartAsync = () => {
   return dispatch => {
      // Dispatch start event
      dispatch(fetchSpecialSportsStart());
      SpecialSportsBook.fetchSpecialSports().then(response => {
         // Dispatch success event
         dispatch(fetchSpecialSportsSuccess(response.data));
      }).catch(error => {
         // Dispatch failure events
         dispatch(fetchSpecialSportsFailure(error.response));
      });
   }
}

