import React, {useEffect, useState} from "react";
import {getLiveFixtures} from "../Services/apis";
import { connect } from "react-redux";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import {faTrophy} from '@fortawesome/free-solid-svg-icons'
import {formatDate, getLiveOdds, getSpread, groupFixtures, slugify} from "../Utils/helpers";
import {LiveEventsOverview, matchStatus} from "../Utils/constants";
import {LiveOdd} from "./Components/LiveOdd";
import {useSelector} from "react-redux";
import LiveEvents from "../Models/http/LiveEvents";
import { groupLiveEvents } from "../Utils/helpers";
import Loader from "./Components/Loader";
import {NavLink} from "react-router-dom";
import {addOddToSlip} from "../Redux/custom/bets/bet-slip/bet-slip.actions";
import {createStructuredSelector} from "reselect";
import {selectBetSlipData} from "../Redux/custom/bets/bet-slip/bet-slip.selectors";
import {
    selectRematchLeagues
    } from "../Redux/custom/sports/rematch/rematchSports.selectors";

const LiveBetting = ({history, betSlip, addOddToSlip, rematchLeagues}) => {
    const [availableSports, setAvailableSports] = useState([]);
    const [sports, setSports] = useState([]);
    const [filteredSports, setFilteredSports] = useState([]);
    const [activeSport, setActiveSport] = useState('all');
    const coupon = useSelector(({couponData}) => couponData.coupon);
    const {SportsbookGlobalVariable, SportsbookBonusList} = useSelector((state) => state.sportsBook);



    const [liveEvents, setLiveEvents] = useState([]);
    const [loadingLiveEvents, setLoadingLiveEvents] = useState(true);
    const [activeLiveSport, setActiveLiveSport] = useState('all');
    const [filteredSportEvents, setFilteredSportEvents] = useState(null);
    const [totalPredictionEvents, setTotalPredictionEvents] = useState([]);


    const [localSports, setLocalSports] = useState([]);

    const getData = () => {
        getLiveFixtures().then(response => {
            // console.log(response);
            setAvailableSports(response.AvailableSports);
            let sports = response.Sports;
            if(sports.length > 0){
                sports.forEach((item, key) => {
                    item.headers = LiveEventsOverview.find(sport => sport.id === item.Id);
                });
                setSports(sports)
            }else{

            }
        })
    }

    const getLiveEvents = async () => {
        await LiveEvents.fetchLocalSports().then(async response => {
            setLocalSports(response.data);

            await LiveEvents.fetchEvents().then(response => {
                setLiveEvents(response.data.data);
                console.log(response.data.data);
            });
        });
    }

    const fetchSportDetails = (sport_id) => {
        return localSports.find(sport => sport.sport_id === sport_id);
    }



    const countSportEvents = async (sport_id) => {
        let events = [];
        liveEvents?.forEach(event => {
            if (event.sport_id === sport_id){
                events.push(event)
            }
        });

        console.log(events);
        return events.length;
    }

    const filterActiveSport = async () => {
        if (activeLiveSport === 'all'){
            setFilteredSportEvents(liveEvents);
            setActiveSport('all');
        }else {
            setActiveSport(fetchSportDetails(activeLiveSport))
            let events = [];
            liveEvents?.forEach(event => {
                if (event.sport_id === activeLiveSport){
                    events.push(event)
                }
            });
            setFilteredSportEvents(events);
        }
    }

    useEffect(() => {
        setLoadingLiveEvents(true);

        const interval = setInterval(() => {
            getLiveEvents();
        }, 5000);

        return () => clearInterval(interval);
    }, []);


    useEffect(() => {
        filterActiveSport().then(response => {
            setLoadingLiveEvents(false);
        });
    }, [activeLiveSport, localSports, liveEvents])

    useEffect(() => {
        getLiveFixtures().then(response => {
            setAvailableSports(response.AvailableSports)
            let sports = response.Sports;
            if(sports.length > 0){
                sports.forEach((item, key) => {
                    item.headers = LiveEventsOverview.find(sport => sport.id === item.Id);
                });
                setSports(sports);
            }else{

            }
        })
    }, []);



    useEffect(() => {
        //
        // const interval = setInterval(() => {
        //     getData();
        // }, 5000);
        //
        // return () => clearInterval(interval);
    }, []);


    useEffect(() => {
        filterSports(activeSport);
    }, [sports]);

    const togglePanel = (e) => {
        const parent = e.currentTarget.parentElement;
        parent.classList.toggle('closed');
    }

    const filterSports = (sport)  => {
        if (sport === 'all') {
            setFilteredSports(sports);
            setActiveSport('all');
        } else {

            const filter = localSports.filter(el => el.name === sport);
            console.log(filter[0]);
            if (filter) {
                setFilteredSports(filter);
            }
        }
    }

    const calculatePredictionEvents = (slip) => {
        var selected = [];
        slip.slip.forEach(league => {
            league.fixtures.forEach(fixture => {
                selected.push(fixture);
            });
        });

        setTotalPredictionEvents(selected);
    }


    useEffect(() => {
        calculatePredictionEvents(betSlip);
    }, [betSlip])


    const isPredictionSelected = (odd, event, leagueName) => {

        // const league =  rematchLeagues.find(league => league.name ===leagueName);
        // console.log(league);

        var isPresent = false;
        betSlip.slip.forEach(selLeague => {
            if (selLeague.league === leagueName){
                totalPredictionEvents?.forEach(preEvent => {
                    if (preEvent.name === event.name){
                        preEvent.predictions?.forEach(pre => {
                            if (pre.sign_key === odd.sign_key){
                                isPresent = true;
                            }
                        });
                    }
                });
            }
        });

        return isPresent;
    }


    return (
        <>
            <div className="topHeader panel-heading">
                <div className="sport-menu">
                    <div
                        onClick={() => setActiveLiveSport('all')}
                        className={`menu all ${activeSport === 'all' ? 'selected' : ''}`}>
                        <FontAwesomeIcon icon={faTrophy} />
                        <span>All</span>
                    </div>
                    {localSports.length > 0 && localSports.map(sport =>
                    <div
                        onClick={() => setActiveLiveSport(sport.sport_id)}
                        className={`menu sport_${sport.name} ${activeSport.sport_id === sport.sport_id ? 'selected' : ''}`} key={sport.sport_id}>
                        <span className={`live-item-icon icon_${sport.sport_id}`} />
                        <span>{sport.name}</span>
                        <span className="sport-count">{() => countSportEvents(sport.sport_id)}</span>
                    </div>)}
                </div>
            </div>
            <div id="live-grouped-odds">
                <div id="CNWrapper">
                    <div className="wrapper">
                        <div className="content" id="divMainContentLive">
                            <div id="divPanelInPlayEvents">
                                <div id="divMainEventsLive">
                                    <div className="match-data-container">
                                        <table className="match-data-table">
                                            <tbody className="match-data-tbody">
                                            <tr className="stat-top">
                                                <th className="match">Upcoming Events</th>
                                                <th colSpan="3">1X2</th>
                                                <th colSpan="3">Double Chance</th>
                                                <th colSpan="2">Over/Under 2.5</th>
                                            </tr>
                                            <tr className="stat-bottom">
                                                <th></th>
                                                <th>1</th>
                                                <th>X</th>
                                                <th>2</th>
                                                <th>1X</th>
                                                <th>12</th>
                                                <th>X2</th>
                                                <th>Over</th>
                                                <th>Under</th>
                                            </tr>
                                            {loadingLiveEvents ?
                                                <tr>
                                                    <td colSpan="9">
                                                        <Loader />
                                                    </td>
                                                </tr>
                                                :
                                                filteredSportEvents && groupLiveEvents(filteredSportEvents).map((fixture, i) =>
                                                    <>
                                                        <tr className="date" key={i}>
                                                            <td colSpan={9}>{fetchSportDetails(fixture.sport_id).name}</td>
                                                        </tr>
                                                        {fixture.events.map(match =>
                                                            ((match.betstop === false)?
                                                                <>
                                                                    <tr className="stat-row" key={`key_${match.search_code}`} id={`event_${match.search_code}`}><td className="match-teams-content">
                                                                        <div className="match-teams-content">
                                                                            <NavLink  to={`/Sport/EventDetail?EventID=${match.search_code}`} className="match-teams-content-a">
                                                                                <span className="match-teams">{match.name}</span>
                                                                            </NavLink>
                                                                            <div className="match-teams-content-b">
                                                                                <span className="match-date">{(match.match_time)?`${match.match_time}'`:null}</span>
                                                                                <span className="match-time">{match.match_period}</span>
                                                                                <span className="match-time" style={{ color: 'green' }}>{match.score.CURRENT}</span>
                                                                            </div>
                                                                        </div>
                                                                    </td>
                                                                        {match.odds.length > 0 && match.odds.map((odd, index)=>
                                                                            index < 8 && (
                                                                                <td key={`prediction-${odd.sign_key}`}>
                                                                                    <span
                                                                                        // onClick={() => selectOdd(odd, match)}
                                                                                        className={`match-value odd quote ${isPredictionSelected(odd, match, match.champ_name)?'selected': null}`}
                                                                                    >{odd.rank}</span>
                                                                                </td>
                                                                            )
                                                                        )}
                                                                    </tr>
                                                                    <tr key={`c_${match.id}`}>
                                                                        <td colSpan="11">
                                                                            <div className={`SingleInsideStats_${match.search_code}`} />
                                                                        </td>
                                                                    </tr>
                                                                </>:null
                                                            )
                                                        )}
                                                    </>
                                                )}
                                            </tbody>
                                        </table>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}


const mapDispatchToProps = dispatch => ({
    addOddToSlip: odd => dispatch(addOddToSlip(odd))
});

const mapStateToProps = createStructuredSelector({
    betSlip: selectBetSlipData,
    rematchLeagues: selectRematchLeagues
});

export default connect(mapStateToProps, mapDispatchToProps)(LiveBetting);