import React from "react";
import useSWR from "swr/esm/use-swr";

export default function ContactUs({match}) {
    const slug = match.params.slug;

    const {data, error} = useSWR(`/utilities/cms/page/${slug}`);

    return (
        <>
            <div id="MainContent" className="sport" style={{padding: '10px'}}>
                <h1>Contact us</h1>
                <p>We are here for you.</p>
                <p>Reach our customer service representatives via a channel that is most appropriate for you:</p>
                <p>Email: betteck27@gmail.com</p>

                <p>TELEPHONE: 08053877646 (8:30 AM - 11:00 PM).</p>
                <p>Whatsapp: 08101212126</p>

                <p><b><i>When sending us a message, please include your full name and your registered phone number</i></b></p>
            </div>
        </>
    )
}
