import { createSelector } from "reselect";

const selectBetSlip = state => state.betSlip;

export const selectBetSlipData = createSelector(
  [selectBetSlip],
  slips => slips.slip
);

export const selectBetSlipFetchingStatus = createSelector(
    [selectBetSlip],
    slips => slips.isFetching
);

export const selectBetType = createSelector(
    [selectBetSlip],
    slips => slips.betType
);


export const selectBetSlipBookError = createSelector(
    [selectBetSlip],
    slips => slips.errorMessage
);