import React, {useState, useEffect} from "react";
import {Formik, Field} from "formik";
import { connect } from "react-redux";
import {changePassword} from "../../Services/apis";
import * as Yup from "yup";
import {useDispatch} from "react-redux";
import {LOADING, REMOVE_USER_DATA, SHOW_MODAL} from "../../Redux/types";
import Auth from "../../Models/http/Auth";
import {toast} from "react-toastify";
import {createStructuredSelector} from "reselect";
import {selectCurrentUser} from "../../Redux/custom/user/user.selectors";
import Transaction from "../../Models/http/Transaction";

const ChangeBankAccountSchema = Yup.object().shape({
    bank: Yup.string()
        .required("Select a bank"),
    account_type: Yup.string()
        .required("Select your account type"),
    account_number: Yup.number('Must be an integer').positive("Negative numbers not allowed")
        .required("Enter your account number"),
});

const BankAccount = ({match, history, currentUser}) => {
    const [errMsgs, setErrMsgs] = useState([]);
    const [account, setAccount] = useState({});

    const dispatch = useDispatch();

    const submitForm = (values, {setSubmitting, resetForm}) => {
        dispatch({type: LOADING});

        Transaction.addBankAccount(values, currentUser.token).then(response => {
            setSubmitting(false);
            if (response.data.errorCode === 'SUCCESS'){
                toast.success('Update successful', {position: 'top-right'});
            }
            setAccount(response.data.data)
            resetForm();
        }).catch(error => {
            setSubmitting(false);
            if (error.response.status === 422){
                console.log(error.response);
                return;
                let errors = Object.values(error.response.data.errors);
                errors = errors.flat();
                errors.forEach(error => {
                    toast.error(error);
                })
            } else {
                if (error.response.data){
                    toast.error(error.response.data.message);
                }else {
                    toast.error(error.message);
                }
            }
        })
    }

    const fetchMyBankAccount = async () => {
        Transaction.fetchMyBankAccount(currentUser.token).then(response => {
            setAccount(response.data.data);
        }).catch(error => {
            console.log(error.response)
        })
    }
    useEffect( () => {
        fetchMyBankAccount();
    })

    return (
        <>
            <div id="MainContent" className="">
                <div className="Riquadro">
                    <div className="CntSX">
                        <div className="CntDX">
                            <div id="s_w_PC_PC_panelSquare">
                                <div className="RiquadroNews Reg">
                                    <div className="Cnt">
                                        <div>
                                            <div id="s_w_PC_PC_upDati">
                                                <div id="s_w_PC_PC_panelPwd">
                                                    <Formik
                                                        enableReinitialize={true}
                                                        initialValues={{
                                                            bank: account.bank,
                                                            account_number: account.account_number,
                                                            account_type: account.account_type,
                                                        }}
                                                        validationSchema={ChangeBankAccountSchema}
                                                        children={(props) => <ChangeBankAccountForm {...props} />}
                                                        onSubmit={submitForm}
                                                    />

                                                </div>
                                            </div>
                                        </div>
                                    </div>

                                    {
                                        (account)?<div className="Cnt">
                                            <div>
                                                <h3 style={{textAlign: "center" }}>Account details</h3>
                                                <p>BANK: {account.bank}</p>
                                                <p>ACCOUNT TYPE: {account.account_type}</p>
                                                <p>ACCOUNT NAME: {account.account_number}</p>
                                            </div>
                                        </div>:null
                                    }

                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </>
    );
}



export function ChangeBankAccountForm({
                                       errors,
                                       touched,
                                       setFieldValue,
                                       handleSubmit,

                                       isSubmitting,
                                       isValid,
                                       values,
                                   }) {
    const {bank, account_type, account_number} = values;

    return (
        <form className="account form p15" id="change-password" onSubmit={handleSubmit} noValidate="novalidate">
            <table width="100%" cellSpacing="0" className="tblPP">
                <tbody>
                <tr>
                    <td colSpan="4" className="cellaInfo">In this section you
                        will be able to modify your bank account details or insert a new account for withdrawals
                    </td>
                </tr>
                <tr>
                    <td className="cellaSx" width="100%">Bank Name</td>
                    <td className="cellaSx"><b>*</b></td>
                    <td className="cellaDx">
                        <Field
                            name="bank"
                            component='select'
                            maxLength="32"
                            value={bank}
                            id="s_w_PC_PC_CurrentPassword"
                            className="textbox"
                            style={{width: '150px' }}
                        >
                            <option value="">Select bank</option>
                            <option value="citibank">Citibank</option>
                            <option value="diamond">Diamond Bank</option>
                            <option value="ecobank">Ecobank</option>
                            <option value="fidelity">Fidelity Bank</option>
                            <option value="firstbank">First Bank</option>
                            <option value="fcmb">First City Monument Bank (FCMB)</option>
                            <option value="gtb">Guaranty Trust Bank (GTB)</option>
                            <option value="heritage">Heritage Bank</option>
                            <option value="keystone">Keystone Bank</option>
                            <option value="polaris">Polaris Bank</option>
                            <option value="providus">Providus Bank</option>
                            <option value="stanbic">Stanbic IBTC Bank</option>
                            <option value="standard">Standard Chartered Bank</option>
                            <option value="sterling">Sterling Bank</option>
                            <option value="suntrust">Suntrust Bank</option>
                            <option value="union">Union Bank</option>
                            <option value="uba">United Bank for Africa (UBA)</option>
                            <option value="unity">Unity Bank</option>
                            <option value="wema">Wema Bank</option>
                            <option value="zenith">Zenith Bank</option>
                        </Field>
                        {errors.bank && touched.bank? (
                            <span id="s_w_PC_PC_reqCurrentPassword" style={{color:'Red'}}>
                                <span className="imgError">{errors.bank}</span>
                            </span>
                        ) : null}
                    </td>
                    <td className="rfv">
                    </td>
                </tr>
                <tr>
                    <td className="cellaSx">Account type</td>
                    <td className="cellaSx"><b>*</b></td>
                    <td className="cellaDx">
                        <Field
                            name="account_type"
                            component='select'
                            maxLength="32"
                            value={account_type}
                            id="s_w_PC_PC_Password"
                            className="textbox"
                            style={{width: '150px'}}
                        >
                            <option value=''>Select account type</option>
                            <option value="Current">Current</option>
                            <option value="Savings">Savings</option>
                        </Field>
                        {errors.account_type && touched.account_type ? (
                            <span id="s_w_PC_PC_reqPassword" style={{color:'Red'}}>
                                <span className="imgError">{errors.account_type}</span>
                            </span>
                        ) : null}
                    </td>
                    <td className="rfv">

                    </td>
                </tr>
                <tr>
                    <td className="cellaSx">Account Number</td>
                    <td className="cellaSx"><b>*</b></td>
                    <td className="cellaDx">
                        <Field
                            name="account_number"
                            type="number"
                            value={account_number}
                            maxLength="32"
                            id="s_w_PC_PC_Confirm"
                            className="textbox"
                            style={{width:'150px'}}
                        />
                        {errors.account_number && touched.account_number ? (
                            <span id="s_w_PC_PC_reqConfirmPassword" style={{color:'Red'}}>
                                <span className="imgError">{errors.account_number}</span>
                            </span>
                        ) : null}
                    </td>
                    <td className="rfv">

                    </td>
                </tr>
                <tr>
                    <td colSpan="4">
                        <div className="spacer5"/>
                    </td>
                </tr>
                <tr>
                    <td align="right" colSpan="3">
                        <input
                            type="button"
                            name="s$w$PC$PC$btnResetPwd"
                            value="Clear" id="s_w_PC_PC_btnResetPwd"
                            className="button"
                        />
                        <button type="submit" disabled={isSubmitting} className="button" id="updatePassword">Confirm</button>
                    </td>
                    <td></td>
                </tr>
                </tbody>
            </table>
        </form>
    )
}


const mapStateToProps = createStructuredSelector({
    currentUser: selectCurrentUser
});

export default connect(mapStateToProps)(BankAccount);
