import { RematchSportsActionTypes } from "./rematchSports.types";

const INITIAL_STATE = {
    rematchSports: null,
    rematchLeagues: null,
    rematchGroups: null,
    isFetching: false,
    errorMessage: null
};

const rematchReducerReducer = (state = INITIAL_STATE, action) => {
    switch (action.type) {
        case RematchSportsActionTypes.FETCH_REMATCH_SPORTS_START:
            return {
                ...state,
                isFetching: true
            }
        case RematchSportsActionTypes.FETCH_REMATCH_SPORTS_SUCCESS:
            return {
                ...state,
                isFetching: false,
                rematchSports: action.payload,
                rematchLeagues: sportData(action.payload).leagues,
                rematchGroups: sportData(action.payload).groups
            }
        case RematchSportsActionTypes.FETCH_REMATCH_SPORTS_FAILURE:
            return {
                ...state,
                isFetching: false,
                errorMessage: action.payload
            }
        default:
            return state;
    }
};

 function sportData(sports) {
     let leagues = [];
     let groups  = [];
     sports.forEach(sport => {
         groups.push(...sport.groups);
         sport.groups.forEach(group => {
             leagues.push(...group.leagues)
         })
     });
     return {
         leagues,
         groups
     };
}
export default rematchReducerReducer;