import axios from "axios";
import Api from "./Api";

class LiveEvents extends Api{

    fetchEvents = async () => {
        return await axios.get(`${this.endPoint()}/sport-book/live/events`, { withCredentials: true });
    }

    fetchOddsStructure = async () => {
        return await axios.get(`${this.endPoint()}/sport-book/live/odds`, { withCredentials: true });
    }

    fetchLocalSports = async () => {
        return await axios.get(`${this.endPoint()}/sport-book/local/prematch/sports`, { withCredentials: true });
    }

}

export default new LiveEvents;