import React, {useEffect, useState} from "react";
import Tablet from "./Components/Tablet";
import {useDispatch, useSelector} from "react-redux";
import {setTournaments} from "../Redux/actions";
import {createStructuredSelector} from "reselect";
import {selectRematchLeagues} from "../Redux/custom/sports/rematch/rematchSports.selectors";
import { connect } from "react-redux";

const SportOdds = ({location, rematchLeagues, history}) =>{
    const urlParam = new URLSearchParams(location.search);
    const [rematchTournaments, setRematchTournaments] =  useState(null);
    const tid = urlParam.get('tid');
    const sid = urlParam.get('sid');
    const dispatch = useDispatch();

    const {tournaments } = useSelector(state => state.sportsData);

    useEffect(() => {
        setRematchTournaments(rematchLeagues);
        if(tid) {
            const tournament = rematchLeagues.find(tournament => tournament.champ_id === parseInt(tid));

            if (!tournament) { // if new tournament, add to store
                dispatch(setTournaments({tid, sid}));
            }
        }
    }, [dispatch, sid, tid, rematchLeagues]);
    

    return (
        <>
            <div id="MainContent" className="sport">
                {tournaments.length === 0 ? (
                    <div className="iSBox ctrl_oddsView" >
                        <div className="oddsViewPanel">
                            <div className="empty">
                                <span>No Events Selected</span>
                            </div>
                        </div>
                    </div>
                ):(
                    tournaments.map((tournament, i) =>
                        <Tablet
                        key={`tournament-${tournament.champ_id}`}
                        index={i}
                        history={history}
                        tournament={tournament}/>)
                )}
            </div>
        </>
    )
}
const mapStateToProps = createStructuredSelector({
    rematchLeagues: selectRematchLeagues,
})

export default connect(mapStateToProps)(SportOdds)