import { BetSlipActionTypes } from "./bet-slip.types";
import AntePostSportsBook from "../../../../Models/http/AntePostSportsBook";

export const fetchCouponStart = couponCode => ({
   type: BetSlipActionTypes.FETCH_COUPON_START,
   payload: couponCode
});

export const fetchCouponSuccess = betSlip => ({
   type: BetSlipActionTypes.FETCH_COUPON_SUCCESS,
   payload: betSlip
});

export const fetchCouponFailure = error => ({
   type: BetSlipActionTypes.FETCH_COUPON_FAILURE,
   payload: error
});


export const addOddToSlip = odd => ({
   type: BetSlipActionTypes.ADD_ODD_TO_SLIP,
   payload: odd
});


