import axios from "axios";
import Api from "./Api";

class SpecialSportsBook extends Api{

    fetchSpecialSports = async () => {
        return await axios.get(`${this.endPoint()}/sport-book/local/special/sports`, { withCredentials: true });
    }

    fetchSpecialLeagueEvents = async champ_id => {
        return await axios.get(`${this.endPoint()}/sport-book/special/events/${champ_id}`, { withCredentials: true });
    }

}

export default new SpecialSportsBook;