import React from "react";
import useSWR from "swr/esm/use-swr";

export default function HowToDeposit({match}) {
    const slug = match.params.slug;

    const {data, error} = useSWR(`/utilities/cms/page/${slug}`);

    return (
        <>
            <div id="MainContent" className="sport" style={{padding: '10px'}}>
                <h1>How To Deposit</h1>
                <p>This deposit method is free and available for everyone. Minimum deposit is 100 Naira and Maximum deposit is 100,000 Naira.</p>
                <p>Reach our customer service representatives via a channel that is most appropriate for you:</p>
                <p>log unto www.actionbet.com</p>

                <p>Step 1: Click on deposit Now if you are using PC, if you are using mobile just click deposit</p>

                <p>Step 2: Enter the amount you wish to deposit</p>

                <p>Step 3: You will be directed to to select a payment method. You need to have the amount entered in your bank account balance.</p>

                <p>Step 4: Upon successful payment your betting wallet will be credited instantly with the deposit amount.</p>

                <p>You can always view the history of transactions in the balance tab of the app</p>

            </div>
        </>
    )
}
