import React, {useEffect, useState} from "react";
import {connect, useDispatch} from "react-redux";
import {PaystackButton} from 'react-paystack'
import {saveTransaction} from "../../Services/apis";
import {UPDATE_USER_BALANCE} from "../../Redux/types";
import {formatNumber} from "../../Utils/helpers";
import {toast} from "react-toastify";
import {useRavePayment} from "react-ravepayment";
import {createStructuredSelector} from "reselect";
import {selectCurrentUser} from "../../Redux/custom/user/user.selectors";
import Transaction from "../../Models/http/Transaction";
import { signInSuccess, signOutStartAsync} from "../../Redux/custom/user/user.actions";

const gateways = [
    {slug: 'rave', name: 'Flutterwave'},
    {slug: 'monnify', name: 'Monnify'},
    {slug: 'paystack', name: 'Paystack'},
];

const Deposit = ({history, currentUser, signInSuccess}) => {
    const {user} = currentUser;
    const [activeTab, setActiveTab] = useState(    {slug: 'monnify', name: 'Monnify'});
    const [config, setConfig] = useState( {
        txref: (new Date()).getTime(),
        customer_email: user.email,
        customer_phone: "",
        amount: '',
        PBFPubKey: "",
        contractCode: '',
        production: process.env.NODE_ENV === 'production',
    });
    const [paymentSuccess, setPaymentSuccess] = useState('');
    const dispatch = useDispatch();

    const updateAmount = (value) => {
        if (value === 0) {
            setConfig({...config, amount: 0});
            return;
        }
        let currentAmount = config.amount;
        if (currentAmount === ''){
            currentAmount = 0;
        }
        const newAmount = currentAmount + value
        setConfig({...config, amount: newAmount});
    }

    const verifyPayment = (response) => {
        if (config.amount > 0) {
            if (response.message === "Approved") {
                setPaymentSuccess(`Success!! Your account has been credited with ${formatNumber(config.amount)}`);
                // update user balance
                dispatch({type: UPDATE_USER_BALANCE, payload: user.balance + config.amount});

                response.paymentMethod = 'paystack';
                response.channel = 'mobile';
                response.amount = config.amount;
                setConfig({...config, amount: ''});

                savePaystackTransaction(response);

                // dispatch({type: SHOW_MODAL, payload: {show: true, type: 'error', message: 'Your'}})
            } else {
                // dispatch({type: SHOW_MODAL, payload: {show: true, type: 'error', message: 'We were unable to process your request'}})
            }
        }
    }

    const getGateway = (gateway) => {
        setActiveTab(gateway);
        Transaction.fetchPaymentGateway(gateway.slug, currentUser.token)
            .then(res => {
            if (res.data.errorCode === 'SUCCESS') {
                // console.log(res.data.data)
                setConfig({
                    ...config,
                    PBFPubKey: res.data.data.public_key,
                    contractCode: gateway.slug === 'monnify' ? res.data.data.contract_code : '',
                })
            } else {
                toast.error(res.message);
            }
        }).catch(err => {
            toast.error(err.message);
        })
    }

    const  savePaystackTransaction = (response) => {
        const {paymentMethod, transaction} = response;
        response.payment_method = paymentMethod;
        response.transaction_code = transaction;
        response.note = `Account Deposit`

        // console.log(response);
        Transaction.saveDepositTransaction(response,  currentUser.token).then(res =>{
            //Update current User with the new balance
            signInSuccess(res.data)
        }).catch(error => {
            toast.error("Something went wrong with the payment!");
        })

    }

    const  saveMonnifyTransaction = (response) => {
        const {authorizedAmount, paymentReference, transactionReference} = response;
        response.amount = authorizedAmount;
        response.reference = paymentReference;
        response.payment_method = 'Monnify';
        response.transaction_code = '';
        response.trxref           = transactionReference;
        response.channel        = 'website';
        response.note = `Account Deposit`

        // console.log(response);
        Transaction.saveDepositTransaction(response,  currentUser.token).then(res =>{
            console.log(res.data);
            //Update current User with the new balance
            signInSuccess(res.data)
        }).catch(error => {
            toast.error("Something went wrong with the payment!");
            // console.log(error)
        })

    }

    const onSuccess = (response) => {
        response.paymentMethod = activeTab.slug;
        response.channel = 'website';
        response.amount = config.amount;

        switch (activeTab.slug) {
            case 'rave':
                // console.log(response);
                if (response.respcode === "00") {
                    response.reference = response.tx.txRef;
                    setPaymentSuccess(`Success!! Your account has been credited with ${formatNumber(config.amount)}`);
                    // update user balance
                    dispatch({type: UPDATE_USER_BALANCE, payload: user.balance + config.amount});

                    saveTransaction(response);
                }
                break;
            case 'paystack':
                if (response.message === "Approved") {
                    console.log(response);
                    setPaymentSuccess(`Success!! Your account has been credited with ${formatNumber(config.amount)}`);
                    // update user balance
                    // dispatch({type: UPDATE_USER_BALANCE, payload: user.balance + config.amount});
                    saveTransaction(response);
                } else {
                }
                break;
            case 'monnify':
                if (response.status === 'SUCCESS') {
                    setPaymentSuccess(`Success!! Your account has been credited with ${formatNumber(config.amount)}`);
                    // update user balance
                    dispatch({type: UPDATE_USER_BALANCE, payload: user.balance + config.amount});
                    response.reference = response.transactionReference;
                    saveMonnifyTransaction(response);
                }
                break;
        }
        setConfig({...config, amount: ''});
    }

    const onClose = () => {
        // console.log('closed');
    }

    useEffect(() => {
        getGateway(activeTab);
    }, []);

    const { initializePayment } = useRavePayment(config);

    function payWithMonnify() {
        window.MonnifySDK.initialize({
            amount: config.amount,
            currency: "NGN",
            reference: '' + Math.floor((Math.random() * 1000000000) + 1),
            customerEmail: user.email,
            apiKey: config.PBFPubKey,
            contractCode: config.contractCode,
            paymentDescription: "Gaming Account funding",
            isTestMode: config.production,
            paymentMethods: ["CARD", "ACCOUNT_TRANSFER"],
            onComplete: function(response){
                //Implement what happens when transaction is completed.
                onSuccess(response);
            },
            onClose: function(data){
                //Implement what should happen when the modal is closed here
                console.log(data);
            }
        });
    }

    return (
        <>
            <div id="MainContent" className="">
                <div className="Riquadro">
                    <div className="CntSX">
                        <div className="CntDX">
                            <div className="payments">
                                <div className="RiquadroSrc">
                                    <div className="Cnt">
                                        <div>
                                            <div className="page__head">
                                                <div className="page__head-item"><h1> Instant Deposit Cards</h1></div>
                                            </div>
                                            <div className="t-menu pl30 pr30">
                                                {gateways.map(gateway =>
                                                <a className={`t-menu__item ${gateway.slug === activeTab.slug ? 'active' : ''}`}
                                                   onClick={() => getGateway(gateway)}
                                                   key={gateway.slug} href="#">
                                                    <strong className="t-menu__item-title"> {gateway.name}</strong>
                                                </a>
                                                )}
                                            </div>
                                            {paymentSuccess !== '' && <div className="info-box green">{paymentSuccess}</div>}
                                            <div className="page__body pt15 pb15">
                                                <div className="wrap mt10">
                                                    <div className="pull-right txt-deepgray"> All fields are required</div>
                                                </div>
                                                <div className="divide-holder">
                                                    <div className="one-half">
                                                        <div className="form-row">
                                                            <div className="form-label"><strong> Deposit
                                                                Amount({process.env.REACT_APP_CURRENCY})</strong></div>
                                                            <div className="form-input">
                                                                <input
                                                                    name="amount"
                                                                    className="big"
                                                                    type="number"
                                                                    autoComplete="off"
                                                                    step="100"
                                                                    maxLength="5"
                                                                    min="100" max="10000"
                                                                    value={config.amount} onChange={(e) => setConfig({...config, amount: e.target.value})}
                                                                />
                                                                <div className="form-input--stake"> Min 100</div>
                                                            </div>
                                                            <div className="quickstake mt10">
                                                                <div className="quickstake__item" onClick={() => updateAmount(0)}> Clear</div>
                                                                <div className="quickstake__item" onClick={() => updateAmount(100)}> +100</div>
                                                                <div className="quickstake__item" onClick={() => updateAmount(200)}> +200</div>
                                                                <div className="quickstake__item" onClick={() => updateAmount(500)}> +500</div>
                                                                <div className="quickstake__item" onClick={() => updateAmount(1000)}> +1000</div>
                                                            </div>
                                                        </div>
                                                        {
                                                            {
                                                                'rave': <button
                                                                    className="btn green mt20 mb20"
                                                                    onClick={() => initializePayment(onSuccess, onClose)}
                                                                > Make Payment</button>,
                                                                'monnify' : <button className="btn green mt20 mb20" onClick={payWithMonnify}> Make Payment</button>,
                                                                'paystack' :  <PaystackButton
                                                                    amount={config.amount * 100}
                                                                    email={user?.email}
                                                                    publicKey={config.PBFPubKey}
                                                                    onSuccess={verifyPayment}
                                                                    text="Make Payment"
                                                                    disabled={parseInt(config.amount) === 0}
                                                                    className="btn green mt20 mb20"
                                                                />
                                                            }[activeTab.slug]
                                                        }
                                                    </div>
                                                    <div className="mt20 pl0 txt-deepgray">
                                                        <h3 className="txt-darkgreen"> Instant {activeTab.name} Deposit</h3>
                                                        <p>There is <strong>no fee for deposits</strong> with this
                                                            payment method. If your transaction is authorized, your
                                                            account will be credited immediately.
                                                        </p>
                                                        <p>
                                                            You can <strong>cancel withdrawals</strong> that have not yet been processed.
                                                        </p>
                                                        <p>
                                                            <strong className="txt-blue undefined"> View Pending
                                                                Withdrawals</strong>
                                                        </p>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </>
    );
}

const mapStateToProps = createStructuredSelector({
    currentUser: selectCurrentUser
});

const mapDispatchToProps = dispatch => ({
    signInSuccess: (user) => dispatch(signInSuccess(user)),
});
export default connect(mapStateToProps, mapDispatchToProps)(Deposit)