import { AntePostSportsActionTypes } from "./antePostSports.types";

const INITIAL_STATE = {
    antePostSports: null,
    antePostGroups: null,
    isFetching: false,
    errorMessage: null
};

const antePostReducerReducer = (state = INITIAL_STATE, action) => {
    switch (action.type) {
        case AntePostSportsActionTypes.FETCH_ANTEPOST_SPORTS_START:
            return {
                ...state,
                isFetching: true
            }
        case AntePostSportsActionTypes.FETCH_ANTEPOST_SPORTS_SUCCESS:
            return {
                ...state,
                isFetching: false,
                antePostSports: action.payload,
                antePostGroups: sportData(action.payload).groups
            }
        case AntePostSportsActionTypes.FETCH_ANTEPOST_SPORTS_FAILURE:
            return {
                ...state,
                isFetching: false,
                errorMessage: action.payload
            }
        default:
            return state;
    }
};


function sportData(sports) {
    let groups  = [];
    sports.forEach(sport => {
        groups.push(...sport.groups);
    });
    return {
        groups
    };
}
export default antePostReducerReducer;