import { LiveEventsActionTypes } from "./liveEvents.types";
import LiveEvents from "../../../../Models/http/LiveEvents";

export const fetchEventsStart = () => ({
   type: LiveEventsActionTypes.FETCH_LIVE_EVENTS_START,
});

export const fetchEventsSuccess = events => ({
   type: LiveEventsActionTypes.FETCH_LIVE_EVENTS_SUCCESS,
   payload: events
});

export const fetchEventsFailure = error => ({
   type: LiveEventsActionTypes.FETCH_LIVE_EVENTS_FAILURE,
   payload: error
});

export const fetchOddsStructureStart = () => ({
   type: LiveEventsActionTypes.FETCH_LIVE_ODDS_STRUCTURE_START,
});

export const fetchOddsStructureSuccess = oddsStructure => ({
   type: LiveEventsActionTypes.FETCH_LIVE_ODDS_STRUCTURE_SUCCESS,
   payload: oddsStructure
});

export const fetchOddsStructureFailure = error => ({
   type: LiveEventsActionTypes.FETCH_LIVE_ODDS_STRUCTURE_FAILURE,
   payload: error
});

/*
   | Functions that implements the action dispatchers
 */

export const fetchLiveEventsStartAsync = () => {
   return dispatch => {
      dispatch(fetchEventsStart());
      LiveEvents.fetchEvents().then(response => {
         dispatch(fetchEventsSuccess(response.data.data))
      }).catch(error => {
         dispatch(fetchEventsFailure(error.response));
      });
   }
}

export const fetchOddsStructureStartAsync = () => {
   return dispatch => {
      // Dispatch start event
      dispatch(fetchOddsStructureStart());
      LiveEvents.fetchOddsStructure().then(response => {
         // Dispatch success event
         dispatch(fetchOddsStructureSuccess(response.data.data))
      }).catch(error => {
         // Dispatch failure event
         dispatch(fetchOddsStructureFailure(error.response));
      });
   }
}

