import { combineReducers } from 'redux';
import { routerReducer } from 'react-router-redux';
import {sportsData, sportsBook} from './sports'
import couponData from './coupon'
import userData from './user'
import login from './login'
import rematchReducerReducer from "../custom/sports/rematch/rematchSportsReducer";
import antePostReducerReducer from "../custom/sports/antepost/antePostSportsReducer";
import specialReducerReducer from "../custom/sports/special/specialSportsReducer";
import liveEventsReducer from "../custom/sports/live/liveEventsReducer";
import userReducer from "../custom/user/userReducer";
import betSlipReducer from "../custom/bets/bet-slip/bet-slip-reducer";

const reducers = combineReducers({
    routing: routerReducer,
    sportsData,
    sportsBook,
    couponData,
    login,
    auth: userData,
    rematch: rematchReducerReducer,
    antePost: antePostReducerReducer,
    special: specialReducerReducer,
    liveEvents: liveEventsReducer,
    user: userReducer,
    betSlip: betSlipReducer
});

export default reducers;
