import React, { useEffect, useState} from 'react';
import TopBets from "./Components/TopBets";
import UpcomingEvents from "./Components/UpcomingEvents";
import {Carousel} from "react-responsive-carousel";
import 'react-responsive-carousel/lib/styles/carousel.css';
import useSWR from "swr/esm/use-swr";
import HomeAdvertBanner from "../Models/http/HomeAdvertBanner";

const Home = () => {
    const [sliders, setSliders] = useState([]);

    const {data: res, error} = useSWR('/sports/banners?banner_type=web');



    useEffect(() => {
        // if (res) {
        //     setSliders(res.sliders);
        // }
        HomeAdvertBanner.fetchBanners(4).then(response => {
            setSliders(response.data.data)
        }).catch(error => {
           console.log(error.response)
        });
    }, [res]);

    return (
        <div className="App-header">
            {sliders.length &&
            <Carousel
                className="banner-container"
                autoPlay
                infiniteLoop={true}
                showStatus={false}
                showThumbs={false}
                showArrows={true}
                interval={5000}
            >
                {sliders.map(slider =>
                    <div key={slider.id}>
                    <a href="#" >
                        <img src={HomeAdvertBanner.imagePath(slider.image)} alt="home banner" />
                    </a>
                    </div>
                )}
            </Carousel>}

            <TopBets />

            {/*<UpcomingEvents />*/}
        </div>
    )
}

export default Home;