const mapDispatchToProps = dispatch => ({
    addOddToSlip: odd => dispatch(addOddToSlip(odd))
});

const mapStateToProps = createStructuredSelector({
    betSlip: selectBetSlipData,
    betType: selectBetType
});

export default connect(mapStateToProps, mapDispatchToProps)(CurrentBetslip);
