import React, {useEffect, useState} from "react";
import {setActivePeriod, setSport, setSports, setTournaments} from "../../../Redux/actions";
import {getSportMenu} from "../../../Services/apis";
import {useDispatch, useSelector} from "react-redux";
import {NavLink, useHistory} from 'react-router-dom';
import {SET_TOURNAMENTS} from "../../../Redux/types";
import {periods} from "../../../Utils/constants";
import {slugify} from "../../../Utils/helpers";
import useSWR from "swr";
import { connect } from "react-redux";
import {createStructuredSelector} from "reselect";
import {
    selectRematchLeagues,
    selectRematchSportsBook
} from "../../../Redux/custom/sports/rematch/rematchSports.selectors";
import {selectAntePostSportsBook} from "../../../Redux/custom/sports/antepost/antePostSports.selectors";
import {selectSpecialSportsBook} from "../../../Redux/custom/sports/special/specialSports.selectors";

const LeftSide = ({ rematchSports, antePostSports, specialSports, rematchLeagues }) => {
    const {activePeriod, sports, tournaments} = useSelector((state) => state.sportsData);

    const [popularGroups, setPopularGroups] = useState([]);

    const [rematchTournaments, setRematchTournaments] =  useState(null);
    const [keyword, setKeyword] = useState('');
    const dispatch = useDispatch();
    const history = useHistory();


    const [showRematch, setShowRematch] = useState(true);
    const [showAntePost, setShowAntePost] = useState(false);
    const [showSpecial, setShowSpecial] = useState(false);

    const {data: topbets, error} = useSWR('sports/top-bets');

    const toggleSportMenu = e => {
        // console.log(e);
        e.currentTarget.parentNode.classList.toggle('active');
        // e.target.nextElementSibling.style.display = ''
    }

    const getSports = async () => {
        await getSportMenu(activePeriod).then(res => {
            dispatch(setSports(res.menu));
        });
    }

    useEffect( () => {
        getSports();
        setRematchTournaments(rematchLeagues);
    }, [activePeriod, rematchLeagues]);

    const openEvent = (cid, tid, sid) => {

        const index = tournaments.findIndex(tournament => tournament.sport_tournament_id === tid);
        if (index !== -1) { // remove tournament
            tournaments.splice(index, 1);
            // update state
            dispatch({
                type: SET_TOURNAMENTS,
                payload: tournaments
            });

        } else {
            const pathname = window.location.pathname;
            const urlSearch= window.location.search;
            const url = `/Sport/Odds?tid=${tid}&sid=${sid}`;
            if (pathname+urlSearch === url) {
                dispatch(setTournaments({tid, sid}));
            } else {
                history.push(url);
            }
        }
        // dispatch(setTournaments({tid, sid}));
    }

    const openRematchEvent = (cid, tid, sid) => {

        const index = rematchTournaments.findIndex(tournament => tournament.champ_id === tid);
        if (index !== -1) { // remove tournament
            let tourn = rematchTournaments.splice(index, 1);
            // update state
            dispatch({
                type: SET_TOURNAMENTS,
                payload: tourn
            });

            console.log('got here');
        } else {
            const pathname = window.location.pathname;
            const urlSearch= window.location.search;
            const url = `/Sport/Odds?tid=${tid}&sid=${sid}`;
            if (pathname+urlSearch === url) {
                dispatch(setTournaments({tid, sid}));
            } else {
                history.push(url);
            }
        }
        dispatch(setTournaments({tid, sid}));
    }

    const doSearch = e => {
        e.preventDefault();
        if(keyword.length)
            history.push(`/Sport/SearchResults?q=${keyword}`)
    }

    const goTo = sport => {
        dispatch(setSport(sport));
        history.push('/Sport/PreMatch/' + slugify(sport.name))
    }

    return (
        <div className="side1">
            <form onSubmit={doSearch} className="search-container">
                <input
                    className="search-input"
                    type="text"
                    maxLength="50"
                    placeholder="Search"
                    value={keyword}
                    onChange={(e) => setKeyword(e.target.value)}
                />
                <button className="search-btn" type="submit">
                    <img
                        className="search-icon"
                        src="/img/icons/search.png"
                        alt="search icon"
                    />
                </button>
            </form>
            <NavLink to="/Live/LiveDefault" className="liveBtn">LiveBetting</NavLink>
            <div className="psec-container">
                <div className="smx-card-section">
                    <div className="smx-card-list-heading">Most popular</div>
                    <div className="smx-card-list">
                        {rematchTournaments && rematchTournaments.slice(0,6).map(league =>
                            <NavLink to={`/Sport/RematchOdds/${league.champ_id}`} key={league.champ_id} className="smx-card" >
                                <span className="smx-item-art-wrap" style={{backgroundImage: 'url(/img/sports-icon/1.png)'}} />
                                <div className="smx-card-details">
                                    <div className="smx-card-name">{league.name}</div>
                                </div>
                            </NavLink>
                        )}
                    </div>
                </div>
            </div>
            <NavLink to="/" className="homeBtn">Home</NavLink>
            {/*<NavLink to="/Sport/OddsLessThan" className="oddBtn">Less then 2</NavLink>*/}
            <NavLink to="/Sport/Explore" className="dailyBtn">Todays matches</NavLink>

            <div className="sport-container">
                <div className="sport-tabs-top">
                    <ul className="sport-tabs" id="filtersport">
                        {periods.map((period, i) => <li
                            key={period.value}
                            onClick={() => dispatch(setActivePeriod(period.value))}
                            className={`s${i} ${activePeriod === period.value ? 'onSel' : ''}`}>
                            <p
                                className={i === 0 || i === 5 ? 'ctr' : ''}
                                dangerouslySetInnerHTML={{ __html: period.label}}
                            />
                        </li> )}

                    </ul>
                </div>

                {
                    (showRematch)?
                        <div className="scx-heading" style={{ cursor: "pointer" }} onClick={() => setShowRematch(false)}>Rematch </div>
                        : <div className="scx-heading" style={{ cursor: "pointer" }} onClick={() => setShowRematch(true)}>Rematch </div>
                }

                {
                    (showRematch)?  <div id="sportMenu" className="sport-tab-content">
                        {rematchSports && rematchSports.map(sport =>
                            <div className="single-sport" key={sport.sport_id}>
                                <div className={`anchor sport-${sport.sport_id}`} onClick={(e) => toggleSportMenu(e)}>
                                    <div className="trigger" />
                                    <div className="icon" />
                                    {sport.name} <span> ({sport.groups.length})</span>
                                    <div className="check">
                                        <input id="sf1" className="sportFlag" type="checkbox"/>
                                        <span className="checkmark" />
                                    </div>
                                </div>
                                <div className="states">
                                    <div className="state" />
                                    {sport?.groups?.map(group =>
                                        <div className="state" key={`category_${group.group_id}`}>
                                            <div className="anchor special" onClick={(e) => toggleSportMenu(e)}>
                                                <div className="trigger">{group.name}</div>
                                                <div className="check">
                                                    <input type="checkbox" />
                                                    <span className="checkmark" />
                                                </div>
                                            </div>
                                            <div className="championships">
                                                {group?.leagues.map(league =>
                                                    <div
                                                        className={`championship ${group.leagues.some(item => item.champ_id === league.champ_id) ? 'selected' : ''}`}
                                                        id={`TOR_${league.champ_id}`}
                                                        key={`TOR_${league.champ_id}`}
                                                        onClick={() => history.push(`/Sport/RematchOdds/${league.champ_id}`)}
                                                        // onClick={(e) => openRematchEvent(group.group_id, league.champ_id, sport.sport_id)}
                                                    >
                                                        <div className="anchor favourite">
                                                            {league.name}
                                                            <div className="check">
                                                                <input type="checkbox" id="TORc_841" />
                                                                <span className="checkmark" />
                                                            </div>
                                                            <div className="fav off" />
                                                        </div>
                                                    </div>)}
                                            </div>
                                        </div>)}
                                </div>
                            </div>)}
                    </div> : null
                }

                {
                    (showAntePost)?
                        <div className="scx-heading" style={{ cursor: "pointer" }} onClick={() => setShowAntePost(false)}>AntePost </div>
                        : <div className="scx-heading" style={{ cursor: "pointer" }} onClick={() => setShowAntePost(true)}>AntePost </div>
                }

                {
                    (showAntePost)?  <div id="sportMenu" className="sport-tab-content">
                        {antePostSports && antePostSports.map(sport =>
                            <div className="single-sport" key={sport.sport_id}>
                                <div className={`anchor sport-${sport.sport_id}`} onClick={(e) => toggleSportMenu(e)}>
                                    <div className="trigger" />
                                    <div className="icon" />
                                    {sport.name} <span> ({sport.groups.length})</span>
                                    <div className="check">
                                        <input id="sf1" className="sportFlag" type="checkbox"/>
                                        <span className="checkmark" />
                                    </div>
                                </div>
                                <div className="states">
                                    <div className="state" />
                                    {sport?.groups?.map(group =>
                                        <div className="state" key={`category_${group.group_id}`}>
                                            <div className="anchor special" onClick={() => history.push(`/Sport/AntePostOdds/${group.group_id}`)}>
                                                <div className="trigger">{group.name}</div>
                                                <div className="check">
                                                    <input type="checkbox" />
                                                    <span className="checkmark" />
                                                </div>
                                            </div>

                                        </div>)}
                                </div>
                            </div>)}
                    </div> : null
                }

                {
                    (showSpecial)?
                        <div className="scx-heading" style={{ cursor: "pointer" }} onClick={() => setShowSpecial(false)}>Special </div>
                        : <div className="scx-heading" style={{ cursor: "pointer" }} onClick={() => setShowSpecial(true)}>Special </div>
                }

                {
                    (showSpecial)?  <div id="sportMenu" className="sport-tab-content">
                        {specialSports && specialSports.map(sport =>
                            <div className="single-sport" key={sport.sport_id}>
                                <div className={`anchor sport-${sport.sport_id}`} onClick={(e) => toggleSportMenu(e)}>
                                    <div className="trigger" />
                                    <div className="icon" />
                                    {sport.name} <span> ({sport.groups.length})</span>
                                    <div className="check">
                                        <input id="sf1" className="sportFlag" type="checkbox"/>
                                        <span className="checkmark" />
                                    </div>
                                </div>
                                <div className="states">
                                    <div className="state" />
                                    {sport?.groups?.map(group =>
                                        <div className="state" key={`category_${group.group_id}`}>
                                            <div className="anchor special" onClick={(e) => toggleSportMenu(e)}>
                                                <div className="trigger">{group.name}</div>
                                                <div className="check">
                                                    <input type="checkbox" />
                                                    <span className="checkmark" />
                                                </div>
                                            </div>
                                            <div className="championships">
                                                {group?.leagues.map(league =>
                                                    <div
                                                        className={`championship ${group.leagues.some(item => item.champ_id === league.champ_id) ? 'selected' : ''}`}
                                                        id={`TOR_${league.champ_id}`}
                                                        key={`TOR_${league.champ_id}`}
                                                        onClick={() => history.push(`/Sport/SpecialOdds/${league.champ_id}`)}
                                                    >
                                                        <div className="anchor favourite">
                                                            {league.name}
                                                            <div className="check">
                                                                <input type="checkbox" id="TORc_841" />
                                                                <span className="checkmark" />
                                                            </div>
                                                            <div className="fav off" />
                                                        </div>
                                                    </div>)}
                                            </div>
                                        </div>)}
                                </div>
                            </div>)}
                    </div>  : null
                }


            </div>
        </div>
    )
}
const mapStateToProps = createStructuredSelector({
    rematchSports: selectRematchSportsBook,
    rematchLeagues: selectRematchLeagues,
    antePostSports: selectAntePostSportsBook,
    specialSports: selectSpecialSportsBook
})
export default connect(mapStateToProps)(LeftSide);