import { LiveEventsActionTypes } from "./liveEvents.types";

const INITIAL_STATE = {
    events: null,
    oddsStructure: null,
    isEventFetching: false,
    isOddStructureFetching: false,
    eventsErrorMessage: null,
    oddsStructureErrorMessage: null
};

const liveEventsReducer = (state = INITIAL_STATE, action) => {
    switch (action.type) {
        case LiveEventsActionTypes.FETCH_LIVE_EVENTS_START:
            return {
                ...state,
                isEventFetching: true
            }
        case LiveEventsActionTypes.FETCH_LIVE_EVENTS_SUCCESS:
            return {
                ...state,
                isEventFetching: false,
                events: action.payload
            }
        case LiveEventsActionTypes.FETCH_LIVE_EVENTS_FAILURE:
            return {
                ...state,
                isEventFetching: false,
                eventsErrorMessage: action.payload
            }
        case LiveEventsActionTypes.FETCH_LIVE_ODDS_STRUCTURE_START:
            return {
                ...state,
                isOddStructureFetching: true
            }
        case LiveEventsActionTypes.FETCH_LIVE_ODDS_STRUCTURE_SUCCESS:
            return {
                ...state,
                isOddStructureFetching: false,
                oddsStructure: action.payload
            }
        case LiveEventsActionTypes.FETCH_LIVE_ODDS_STRUCTURE_FAILURE:
            return {
                ...state,
                isOddStructureFetching: false,
                oddsStructureErrorMessage: action.payload
            }
        default:
            return state;
    }
};

export default liveEventsReducer;