import axios from "axios";
import Api from "./Api";
import React from 'react';
import Cookies from 'js-cookie';

class Auth extends Api{
    constructor() {
        super();
        this.userToken = Cookies.get('token')
    }

    authenticateUser = async (username, password) => {
        return await axios.post(`${this.endPoint()}/sign-in`, {
            username,
            password
        },{ withCredentials: true });
    }


    verifyResetPasswordEmail = async (email) => {
        return await axios.post(`${this.endPoint()}/reset-password`,{
            email
        },{ withCredentials: true });
    }

    chooseNewPassword = async (resetToken, password, passwordConfirm) => {
        return await axios.post(`${this.endPoint()}/choose-new-password/${resetToken}`,{
            password,
            password_confirmation: passwordConfirm
        },{ withCredentials: true });
    }

    verifyAccount = async token => {
        return await axios.post(`${this.endPoint()}/verify-user/${token}`,{ withCredentials: true });
    }

    registerUser = async (values) => {
        console.log(values);
        return await axios.post(`${this.endPoint()}/sign-up`, {
            username: values.username,
            lastname: values.full_name.split(' ')[0],
            firstname: values.full_name.split(' ')[1],
            email: values.email,
            phone: values.phone,
            password: values.password,
            password_confirmation: values.confirm_password
        },{ withCredentials: true });
    }

    updatePlayerAccount = async (values, userToken) => {
        values.phone =  values.phone_number;
        console.log(userToken);
        return await axios.post(`${this.endPoint()}/update-player`, values,{
            withCredentials: true,
            headers: {
                Authorization: `Bearer ${userToken}`
            }
        });
    }

    updateUserPassword = async (values, userToken) => {
        return await axios.post(`${this.endPoint()}/change-password`, values,{
            withCredentials: true,
            headers: {
                Authorization: `Bearer ${userToken}`
            }
        });
    }


    registerAgency = async (values) => {
        console.log(values);
        return await axios.post(`${this.endPoint()}/agency/sign-up`, {
            username: values.username,
            email: values.email,
            lastname: values.first_name,
            firstname: values.last_name,
            gender: values.gender,
            date_of_birth: values.date_of_birth,
            personal_address: values.personal_address,
            shop_address: values.shop_address,
            state: values.state,
            phone: values.phone,
            password: values.password,
            password_confirmation: values.password_confirmation
        },{ withCredentials: true });
    }

    updateUser = async (formData) => {
        return await axios.patch(`${this.endPoint()}/users/update-me`, formData,{ withCredentials: true });
    }

    updatePassword = async (passwordCurrent, password, passwordConfirm) => {
        return await axios.patch(`${this.endPoint()}/users/update-password`, {
            passwordCurrent,
            password,
            passwordConfirm
        },{ withCredentials: true });
    }


    updateSettings = async (...data) => {
        return await axios.post(`${this.endPoint()}/system`, { data
        },{ withCredentials: true })
            .then(response => {
                return response
            })
            .catch(error => {
                // console.log(error.response.data)
                return error
            });
    }

    isUserLoggedIn = async () => {
        return await axios.post(`${this.endPoint()}/is-logged-in`, {},{ withCredentials: true });
    }

    logout = async (token) => {
        console.log(token);
        await axios.post(`${this.endPoint()}/sign-out`, {},{
            withCredentials: true,
            headers: {
                Authorization: `Bearer ${token}`
            }
        });
    }


    updateUserPrimaryData = async (data) => {
        delete data.email;
        return await axios.patch(`${this.endPoint()}/users/update-me`, data,{
            withCredentials: true,
            headers: {
                'Content-Type': 'multipart/form-data'
            },
        });
    }

}

export default new Auth;