import { BetSlipActionTypes } from "./bet-slip.types";

const INITIAL_STATE = {
    slip: {
        type: null,
        slip: [

        ]
    },
    betType: null,
    isFetching: false,
    errorMessage: null
};

const betSlipReducer = (state = INITIAL_STATE, action) => {
    switch (action.type) {
        case BetSlipActionTypes.FETCH_COUPON_START:
            return {
                ...state,
                isFetching: true
            }
        case BetSlipActionTypes.ADD_ODD_TO_SLIP:
            return {
                ...state,
                isFetching: false,
                betType: computeBetType(state.slip, action.payload),
                slip: computeSlip(state.slip, action.payload),
            }
        default:
            return state;
    }
};

const computeSlip = (currentSlip, odd) => {

    let itemFixtureAndPrediction = {
        ...odd.fixture.event,
        predictions: [
            odd.fixture.prediction
        ]
    }
    let item = {
        league: odd.league,
        group: odd.leagueGroup,
        fixtures: [
           itemFixtureAndPrediction
        ]
    }

   let newSlip = [];
   var leagueFound = false;

    // Check if the league already exist, the append the event to it's event list
    currentSlip.slip.forEach(slip => {
        if (slip.league === odd.league){
            // Check if the event exist in the league
            var fixtureFound = false
            slip.fixtures.forEach((fix, index, object) => {
                if (fix.name === odd.fixture.event.name){
                    // Set fixture found equal to true
                    fixtureFound = true;
                   // Check if the prediction exist
                    var predictionFound = false;
                    fix.predictions.forEach((pred, index, object) => {
                        if (pred.sign_key === odd.fixture.prediction.sign_key){
                            object.splice(index, 1);
                            // Check if its the last prediction of the event, then remove the event
                            slip.fixtures.forEach((fix, index, object) => {
                                if (fix.predictions.length < 1){
                                    object.splice(index, 1);
                                }
                            });

                            // Check if it is the last event in the league, then delete the league
                            currentSlip.slip.forEach((slip, index, object) => {
                                if (slip.fixtures.length < 1){
                                    object.splice(index, 1);
                                }
                            })
                            predictionFound = true;
                        }
                    });
                    // Add the prediction to its predictions, if not exist
                    if (!predictionFound){
                        fix.predictions.push(odd.fixture.prediction)
                    }
                }
            });
            if (!fixtureFound){
                slip.fixtures.push(itemFixtureAndPrediction)
            }
            leagueFound = true;
        }
    });

    // If league is not found, add the league to betslip
    if (!leagueFound){
        return {
            type: computeBetType([...currentSlip.slip, item]),
            slip: [...currentSlip.slip, item]
        }
    }
    // If league if found, returned the modified version of the betslip
   return {
        type: computeBetType([...currentSlip.slip]),
        slip: [...currentSlip.slip]
    }
}

const computeBetType = (currentSlip, odd) => {
    // let currentSlip = computeSlip(betSlip, odd);

    let betType = 'no-bet';

    if (currentSlip.length > 0){
        // Check if there is only one league
        if (currentSlip.length === 1){
            currentSlip.forEach(league => {
                // Check if there is only one fixtures
                if (league.fixtures.length === 1){
                    // Check if there is only one prediction for the fixture
                    league.fixtures.forEach((fix, index, object) => {
                        // If there is just one prediction
                        if (fix.predictions.length === 1){
                            betType = 'Single';
                        }else {
                            betType = 'Split'
                        }
                    });
                }else {
                    // If we have multiple events in the league
                    let predictionCount = 0;
                    league.fixtures.forEach((fix, index, object) => {
                        // Count the predictions to check if it greater than the number of fixtures
                        fix.predictions.forEach(pred => {
                            predictionCount++;
                        });
                    });
                    if (predictionCount === league.fixtures.length){
                        betType = 'Multiple'
                    }else {
                        betType = 'Split'
                    }
                }
            });
        }else{
            currentSlip.forEach(league => {
                // Check if there is only one fixtures
                if (league.fixtures.length === 1){
                    // Check if there is only one prediction for the fixture
                    league.fixtures.forEach((fix, index, object) => {
                        if (fix.predictions.length === 1){
                            betType = 'Multiple';
                        }else {
                            betType = 'Split';
                        }
                    });
                }else {
                    let predictionCount = 0;
                    league.fixtures.forEach((fix, index, object) => {
                        fix.predictions.forEach(pred => {
                           predictionCount++;
                        });
                    });
                    if (predictionCount === league.fixtures.length){
                        betType = 'Multiple'
                    }else {
                        betType = 'Split'
                    }
                }
            });
        }
    }

    return betType;
}

export default betSlipReducer;