import React, {useState} from "react";
import {Formik, Field} from "formik";
import { connect } from "react-redux";
import {changePassword} from "../../Services/apis";
import * as Yup from "yup";
import {useDispatch} from "react-redux";
import {LOADING, REMOVE_USER_DATA, SHOW_MODAL} from "../../Redux/types";
import Auth from "../../Models/http/Auth";
import {toast} from "react-toastify";
import {createStructuredSelector} from "reselect";
import {selectCurrentUser} from "../../Redux/custom/user/user.selectors";

const ChangePasswordSchema = Yup.object().shape({
    current_password: Yup.string()
        .min(3, "Minimum 3 letters")
        .required("Enter your old password"),
    password: Yup.string()
        .min(3, "Minimum 4 letters")
        .required("Enter a new password"),
    password_confirmation: Yup.string()
        .min(3, "Minimum 4 letters")
        .oneOf([Yup.ref('password'), null], 'Passwords must match')
        .required("Confirm your new password"),
});

const ChangePassword = ({match, history, currentUser}) => {
    const [errMsgs, setErrMsgs] = useState([]);
    const dispatch = useDispatch();

    const submitForm = (values, {setSubmitting, resetForm}) => {
        dispatch({type: LOADING});

        Auth.updateUserPassword(values, currentUser.token).then(response => {
            setSubmitting(false);
            if (response.data.errorCode === 'SUCCESS'){
                toast.success('Update successful', {position: 'top-right'});
            }
            resetForm();
        }).catch(error => {
            setSubmitting(false);
            if (error.response.status === 422){
                console.log(error.response);
                return;
                let errors = Object.values(error.response.data.errors);
                errors = errors.flat();
                errors.forEach(error => {
                    toast.error(error);
                })
            } else {
                if (error.response.data){
                    toast.error(error.response.data.message);
                }else {
                    toast.error(error.message);
                }
            }
        })
    }

    return (
        <>
            <div id="MainContent" className="">
                <div className="Riquadro">
                    <div className="CntSX">
                        <div className="CntDX">
                            <div id="s_w_PC_PC_panelSquare">
                                <div className="RiquadroNews Reg">
                                    <div className="Cnt">
                                        <div>
                                            <div id="s_w_PC_PC_upDati">
                                                <div id="s_w_PC_PC_panelPwd">
                                                    <Formik
                                                        enableReinitialize={true}
                                                        initialValues={{
                                                            current_password: '',
                                                            password: '',
                                                            password_confirmation: '',
                                                        }}
                                                        validationSchema={ChangePasswordSchema}
                                                        children={(props) => <ChangePasswordForm {...props} />}
                                                        onSubmit={submitForm}
                                                    />

                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </>
    );
}



export function ChangePasswordForm({
                                       errors,
                                       touched,
                                       setFieldValue,
                                       handleSubmit,

                                       isSubmitting,
                                       isValid,
                                       values,
                                   }) {
    const {password, current_password, password_confirmation} = values;

    return (
        <form className="account form p15" id="change-password" onSubmit={handleSubmit} noValidate="novalidate">
            <table width="100%" cellSpacing="0" className="tblPP">
                <tbody>
                <tr>
                    <td colSpan="4" className="cellaInfo">In this section you
                        will be able to modify your password you insert a
                        different new password that or from the precedence.
                    </td>
                </tr>
                <tr>
                    <td className="cellaSx" width="100%">Current Password</td>
                    <td className="cellaSx"><b>*</b></td>
                    <td className="cellaDx">
                        <Field
                            name="current_password"
                            type="password"
                            maxLength="32"
                            value={current_password}
                            id="s_w_PC_PC_CurrentPassword"
                            className="textbox"
                            style={{width: '150px' }}
                        />
                        {errors.current_password && touched.current_password? (
                            <span id="s_w_PC_PC_reqCurrentPassword" style={{color:'Red'}}>
                                <span className="imgError">{errors.current_password}</span>
                            </span>
                        ) : null}
                    </td>
                    <td className="rfv">
                    </td>
                </tr>
                <tr>
                    <td className="cellaSx">Password</td>
                    <td className="cellaSx"><b>*</b></td>
                    <td className="cellaDx">
                        <Field
                            name="password"
                            type="password"
                            maxLength="32"
                            value={password}
                            id="s_w_PC_PC_Password"
                            className="textbox"
                            style={{width: '150px'}}
                        />
                        {errors.password && touched.password ? (
                            <span id="s_w_PC_PC_reqPassword" style={{color:'Red'}}>
                                <span className="imgError">{errors.password}</span>
                            </span>
                        ) : null}
                    </td>
                    <td className="rfv">

                    </td>
                </tr>
                <tr>
                    <td className="cellaSx">Confirm Password</td>
                    <td className="cellaSx"><b>*</b></td>
                    <td className="cellaDx">
                        <Field
                            name="password_confirmation"
                            type="password"
                            value={password_confirmation}
                            maxLength="32"
                            id="s_w_PC_PC_Confirm"
                            className="textbox"
                            style={{width:'150px'}}
                        />
                        {errors.password_confirmation && touched.password_confirmation ? (
                            <span id="s_w_PC_PC_reqConfirmPassword" style={{color:'Red'}}>
                                <span className="imgError">{errors.password_confirmation}</span>
                            </span>
                        ) : null}
                    </td>
                    <td className="rfv">

                    </td>
                </tr>
                <tr>
                    <td colSpan="4">
                        <div className="spacer5"/>
                    </td>
                </tr>
                <tr>
                    <td align="right" colSpan="3">
                        <input
                            type="button"
                            name="s$w$PC$PC$btnResetPwd"
                            value="Clear" id="s_w_PC_PC_btnResetPwd"
                            className="button"
                        />
                        <button type="submit" disabled={isSubmitting} className="button" id="updatePassword">Confirm</button>
                    </td>
                    <td></td>
                </tr>
                </tbody>
            </table>
        </form>
    )
}


const mapStateToProps = createStructuredSelector({
    currentUser: selectCurrentUser
});

export default connect(mapStateToProps)(ChangePassword);
