import { SpecialSportsActionTypes } from "./specialSports.types";

const INITIAL_STATE = {
    specialSports: null,
    specialLeagues: null,
    specialGroups: null,
    isFetching: false,
    errorMessage: null
};

const specialReducerReducer = (state = INITIAL_STATE, action) => {
    switch (action.type) {
        case SpecialSportsActionTypes.FETCH_SPECIAL_SPORTS_START:
            return {
                ...state,
                isFetching: true
            }
        case SpecialSportsActionTypes.FETCH_SPECIAL_SPORTS_SUCCESS:
            return {
                ...state,
                isFetching: false,
                specialSports: action.payload,
                specialLeagues: sportData(action.payload).leagues,
                specialGroups: sportData(action.payload).groups
            }
        case SpecialSportsActionTypes.FETCH_SPECIAL_SPORTS_FAILURE:
            return {
                ...state,
                isFetching: false,
                errorMessage: action.payload
            }
        default:
            return state;
    }
};

function sportData(sports) {
    let leagues = [];
    let groups  = [];
    sports.forEach(sport => {
        groups.push(...sport.groups);
        sport.groups.forEach(group => {
            leagues.push(...group.leagues)
        })
    });
    return {
        leagues,
        groups
    };
}

export default specialReducerReducer;