import { UserActionTypes } from "./user.types";
import Auth from "../../../Models/http/Auth";
import {toast} from "react-toastify";
import history from "../../../history";


export const setCurrentUser = user => ({
   type: UserActionTypes.SET_CURRENT_USER,
   payload: user
});

export const signInSuccess = user => ({
   type: UserActionTypes.SIGN_IN_SUCCESS,
   payload: user
});

export const signInFailure = error => ({
   type: UserActionTypes.SIGN_IN_FAILURE,
   payload: error
});

export const userSessionFailure = error => ({
   type: UserActionTypes.USER_SESSION_FAILURE,
   payload: error
});

export const signInStart = () => ({
   type: UserActionTypes.SIGN_IN_START,
});

export const checkUserSession = () => ({
   type: UserActionTypes.CHECK_USER_SESSION
});

export const signOutStart = () => ({
   type: UserActionTypes.SIGN_OUT_START,
});

export const signOutSuccess = () => ({
   type: UserActionTypes.SIGN_OUT_SUCCESS,
});

export const signOutFailure = (error) => ({
   type: UserActionTypes.SIGN_OUT_FAILURE,
   payload: error
});


export const updatePasswordStart = passwordCredentials => ({
   type: UserActionTypes.PASSWORD_UPDATE_START,
   payload: passwordCredentials
});

export const updatePasswordSuccess = user => ({
   type: UserActionTypes.PASSWORD_UPDATE_SUCCESS,
   payload: user
});

export const updatePasswordFailure = error => ({
   type: UserActionTypes.PASSWORD_UPDATE_FAILURE,
   payload: error
});


export const updateUserPrimaryProfileStart = formData => ({
   type: UserActionTypes.UPDATE_USER_PRIMARY_PROFILE_START,
   payload: formData
});

export const updateUserPrimaryProfileSuccess = (userPrimaryData) => ({
   type: UserActionTypes.UPDATE_USER_PRIMARY_PROFILE_SUCCESS,
   payload: userPrimaryData
});

export const updateUserPrimaryProfileFailure = (error) => ({
   type: UserActionTypes.UPDATE_USER_PRIMARY_PROFILE_FAILURE,
   payload: error
});




/*
   | Functions that implements the action dispatchers
 */

export const signInStartAsync = (username, password) => {
   return dispatch => {
      // Dispatch Start event
      dispatch(signInStart());
      Auth.authenticateUser(username, password).then(response => {
         console.log(response.data);
         toast.success("Logged in successfully", {position: 'top-right'});
         // Dispatch success event
         dispatch(signInSuccess(response.data))

      }).catch(error => {
         console.log(error.response);
         toast.error(error.response.data.message, {position: 'top-right'});
         dispatch(signInFailure(error.response))
      })
   }
}

export const signOutStartAsync = (token) => {
   return dispatch => {
      // Dispatch Start event
      dispatch(signOutStart());
      Auth.logout(token).then(response => {
         console.log(response);
         toast.success("Logged out successfully", {position: 'top-right'});
         // Dispatch success event
         dispatch(signOutSuccess(response))
         history.push('/');
      }).catch(error => {
         console.log(error);
         // toast.error(error.response.data.message, {position: 'top-right'});
         dispatch(signOutFailure(error))
      })
   }
}


export const checkUserSessionAsync = () => {
   return dispatch => {
      // Dispatch Start event
      dispatch(checkUserSession());
      Auth.isUserLoggedIn().then(response => {
         toast.success("Session restored", {position: 'bottom-right'});
         // Dispatch success event
         dispatch(signInSuccess(response.data))
      }).catch(error => {
         // console.log(error.response);
         // toast.error(error.response.data.message, {position: 'top-right'});
         dispatch(signOutFailure(error.response))
      })
   }
}



