import axios from "axios";
import Api from "./Api";

class RematchSportsBook extends Api{

    fetchRematchSports = async () => {
        return await axios.get(`${this.endPoint()}/sport-book/local/prematch/sports`, { withCredentials: true });
    }

    fetchRematchLeagueEvents = async champ_id => {
        return await axios.get(`${this.endPoint()}/sport-book/prematch/events/${champ_id}`, { withCredentials: true });
    }

    fetchRematchLeagueOddsStructureFromLocalDb = async champ_id => {
        return await axios.get(`${this.endPoint()}/sport-book/local/prematch/odds-structure/${champ_id}`, { withCredentials: true });
    }

}

export default new RematchSportsBook;