import axios from "axios";
import Api from "./Api";
import React from 'react';
import Cookies from 'js-cookie';

class Transaction extends Api{

    fetchPaymentGateway = async (name, userToken) => {
        return await axios.post(`${this.endPoint()}/payment/fetch-gateway`, {
            name
        },{
            withCredentials: true,
            headers: {
                Authorization: `Bearer ${userToken}`
            }
        });
    }

    saveDepositTransaction = async (values, userToken) => {
        return await axios.post(`${this.endPoint()}/payment/deposit`, values,{
            withCredentials: true,
            headers: {
                Authorization: `Bearer ${userToken}`
            }
        });
    }

    addBankAccount = async (values, userToken) => {
        return await axios.post(`${this.endPoint()}/payment/add-bank-account`, values,{
            withCredentials: true,
            headers: {
                Authorization: `Bearer ${userToken}`
            }
        });
    }

    fetchMyBankAccount = async (userToken) => {
        return await axios.get(`${this.endPoint()}/payment/fetch-my-bank-account`, {
            withCredentials: true,
            headers: {
                Authorization: `Bearer ${userToken}`
            }
        });
    }

    requestWithdrawal = async (values, userToken) => {
        return await axios.post(`${this.endPoint()}/payment/request-withdrawal`, values, {
            withCredentials: true,
            headers: {
                Authorization: `Bearer ${userToken}`
            }
        });
    }


}

export default new Transaction;