import React from "react";
import useSWR from "swr/esm/use-swr";

export default function Faq({match}) {
    const slug = match.params.slug;

    const {data, error} = useSWR(`/utilities/cms/page/${slug}`);

    return (
        <>
            <div id="MainContent" className="sport" style={{padding: '10px'}}>
                <h1>FAQ</h1>
                <p><b><u>Frequently Asked Questions</u></b></p>

                <p>How to Register On the Website?</p>

                <p>Visit www.actionbet.com and click on "Register" displayed in yellow at the top right corner of
                    the screen and then follow the simple steps outlined there.</p>

                <p>Can I Change My Username?<br />
                    Unfortunately no; the username that you have chosen during the registration process cannot be changed.</p>

                <p>Forgotten Login Details?<br />
                    To retrieve your password, please click on "Forgot your Password?" IT can be found on the Home page in the "Sign In" section.</p>

                <p>How to Change Password?<br />
                    Login into your account, go to "player profile" section, click on the "Change Password" and follow the steps. You can also select forgot password on the login form
                </p>

                <p>correct personal details/email how can I update them?<br />
                    In case of inaccuracies, please contact our Customer Services to receive the correct procedure.

                </p>

            </div>
        </>
    )
}
