import { RematchSportsActionTypes } from "./rematchSports.types";
import RematchSportsBook from "../../../../Models/http/RematchSportsBook";

export const fetchRematchSportsStart = () => ({
   type: RematchSportsActionTypes.FETCH_REMATCH_SPORTS_START,
});

export const fetchRematchSportsSuccess = sportsBook => ({
   type: RematchSportsActionTypes.FETCH_REMATCH_SPORTS_SUCCESS,
   payload: sportsBook
});

export const fetchRematchSportsFailure = error => ({
   type: RematchSportsActionTypes.FETCH_REMATCH_SPORTS_FAILURE,
   payload: error
});

/*
   | Functions that implements the action dispatchers
 */

export const fetchRematchSportsStartAsync = () => {
   return dispatch => {
      // Dispatch Start event
      dispatch(fetchRematchSportsStart());
      RematchSportsBook.fetchRematchSports().then(response => {
         // Dispatch success event
         dispatch(fetchRematchSportsSuccess(response.data))

      }).catch(error => {
         // Dispatch failure event
         dispatch(fetchRematchSportsFailure(error.response))
      });
   }
}

