import { createSelector } from "reselect";

const selectAntePostSports = state => state.antePost;

export const selectAntePostSportsBook = createSelector(
  [selectAntePostSports],
  sports => sports.antePostSports
);

export const selectAntePostGroups = createSelector(
    [selectAntePostSports],
    sports => sports.antePostGroups
);

export const selectAntePostSportsBookFetchingStatus = createSelector(
    [selectAntePostSports],
    sports => sports.isFetching
);

export const selectAntePostSportsBookError = createSelector(
    [selectAntePostSports],
    sports => sports.errorMessage
);