import React, { useEffect, useState} from 'react';
import {useDispatch, useSelector} from "react-redux";
import { connect } from "react-redux";

import {
    formatDate,
    getOdds, getStats,
    groupFixtures,
    isSelected,
} from "../../Utils/helpers";
import {NavLink} from "react-router-dom";
import Loader from "./Loader";
import useSWR from "swr/esm/use-swr";
import {createStructuredSelector} from "reselect";
import {
    selectRematchGroups,
    selectRematchLeagues,
    selectRematchSportsBook
} from "../../Redux/custom/sports/rematch/rematchSports.selectors";
import RematchSportsBook from "../../Models/http/RematchSportsBook";
import {selectBetSlipData} from "../../Redux/custom/bets/bet-slip/bet-slip.selectors";
import {addOddToSlip} from "../../Redux/custom/bets/bet-slip/bet-slip.actions";

const TopBets = ({ rematchGroups, rematchLeagues, betSlip, addOddToSlip }) => {
    const [topBets, setTopBets] = useState(null);

    const [topGroups, setTopGroups] = useState(null);

    const [loading, setLoading] = useState(true);
    const [fixtures, setFixtures] = useState([]);


    const [activeTournament, setActiveTournament] = useState([]);
    const [activeGroup, setActiveGroup] = useState([]);
    const [activeLeague, setActiveLeague] = useState([]);

    const dispatch = useDispatch();
    const coupon = useSelector(({couponData}) => couponData.coupon);
    const [predictions, setPredictions] = useState([]);
    const [totalPredictionEvents, setTotalPredictionEvents] = useState([]);

    const [oddsStructure, setOddsStructure] = useState([]);
    const [events, setEvents] = useState([]);

    const {data: res, error} = useSWR('/sports/topbets');

    useEffect(() => {
        if (res) {
            setLoading(false);
            setActiveTournament(res.topbets[0]?.tournament_id)
            setTopBets(res);
            setPredictions(res.predictions);
        }
        if (rematchGroups){
            setTopGroups(rematchGroups);
        }
        setActiveGroupAndLeague();
    }, [res, rematchGroups, rematchLeagues])

    useEffect(() => {
        calculatePredictionEvents(betSlip);
    }, [betSlip])

    const setActiveGroupAndLeague = async () => {
        if (rematchGroups){
            rematchGroups.forEach((group, index) => {
                if (index === 0){
                    setActiveTournament(group.group_id);
                    fetchLeagueEvents(87533);
                }
            });
        }
    }


    const fetchLeagueEvents = async (champ_id) => {
        setLoading(true);
        RematchSportsBook.fetchRematchLeagueEvents(champ_id).then(response => {
            setEvents(response.data.data.events);
            setOddsStructure(response.data.data.oddstructure);
            // console.log(response.data.data.oddstructure);
            // console.log(response.data.data.events);
            setLoading(false);
        }).catch(error => {
            setLoading(false);
            console.log(error.response);
        })
    }

    useEffect(() => {
        if(activeTournament) {
            const events = topBets?.events.filter(item => item.sport_tournament_id === activeTournament);
            setFixtures(events);
        }
    }, [activeTournament, topBets]);


    const calculatePredictionEvents = (slip) => {
        var selected = [];
        slip.slip.forEach(league => {
            league.fixtures.forEach(fixture => {
                selected.push(fixture);
            });
        });

        setTotalPredictionEvents(selected);
    }

    const isPredictionSelected = (odd, event, league) => {
        var isPresent = false;
        betSlip.slip.forEach(selLeague => {
            if (selLeague.league === league.name){
                totalPredictionEvents?.forEach(preEvent => {
                    if (preEvent.name === event.name){
                        preEvent.predictions?.forEach(pre => {
                            if (pre.sign_key === odd.sign_key){
                                isPresent = true;
                            }
                        });
                    }
                });
            }
        });

        return isPresent;
    }

    const selectOdd = async (odd, event) => {
        addOddToBestSlip(betSlip, odd, event)
    }

    const addOddToBestSlip = (betSlip, odd, event) => {
        // If league does not exist on the betslip
        let item = {
            id: `${activeGroup.name}_${activeLeague.name}_${event.name}_${odd.sign_key}`,
            leagueGroup: activeGroup.name,
            league: activeLeague.name,
            fixture: {
                event: event,
                prediction: odd
            }

        }
        // Check if the league exists
        addOddToSlip(item);
        return true;
    }

    const logActiveTournament = () =>{
        console.log(activeTournament);
    }

    return (

        <div className="topbets-container">
            <div className="topbets-top">
                <h4>Top Bets</h4>
                <div id="sb_1" data-id="1" className="topbets-tab active">
                    <a href="#">Football</a>
                </div>
            </div>
            <div className="topbets-bottom">
                {topGroups && topGroups.slice(0, 10).map(group =>
                    <div
                        onClick={() => setActiveTournament(group?.group_id)}
                        className={`topbets-cell sg_1 ${group?.group_id === activeTournament ? 'selected' : ''}`}
                        key={`topbet-item-${group.group_id}`}>
                        <p className="nomeGruppo ng-binding">{group.name}</p>
                        {group?.leagues.slice(0, 1).map(league =>
                            <h4 className="nomeEvento ng-binding" onClick={() =>  {
                                fetchLeagueEvents(league.champ_id);
                                setActiveLeague(league);
                                setActiveGroup(group);
                            }}>{league.name}</h4>
                        )}
                    </div>
                )}
            </div>
            <div className="match-data-container">
                <table className="match-data-table">
                    <tbody className="match-data-tbody">
                    <tr className="stat-top">
                        <th className="match">Upcoming Events</th>
                        <th colSpan="3">1X2</th>
                        <th colSpan="3">Double Chance</th>
                        <th colSpan="2">Over/Under 2.5</th>
                    </tr>
                    <tr className="stat-bottom">
                        <th></th>
                        <th>1</th>
                        <th>X</th>
                        <th>2</th>
                        <th>1X</th>
                        <th>12</th>
                        <th>X2</th>
                        <th>Over</th>
                        <th>Under</th>
                    </tr>
                    {loading ?
                        <tr>
                            <td colSpan="9">
                                <Loader />
                            </td>
                        </tr>
                    :
                    events && groupFixtures(events).map((fixture, i) =>
                        <>
                        <tr className="date" key={i}>
                            <td colSpan={9}>{formatDate(fixture.startdate, 'dddd DD, MMMM')}</td>
                        </tr>
                        {fixture.events.map(match =>
                            <>
                                <tr className="stat-row" key={`key_${match.search_code}`} id={`event_${match.search_code}`}><td className="match-teams-content">
                                        <div className="match-teams-content">
                                            <NavLink  to={`/Sport/EventDetail?EventID=${match.search_code}`} className="match-teams-content-a">
                                                <span className="match-teams">{match.name}</span>
                                            </NavLink>
                                            <div className="match-teams-content-b">
                                                <span className="match-date">{match.search_code}</span>
                                                <span className="match-time">{formatDate(match.startdate, 'HH:mm')}</span>
                                                <a className="match-grap"
                                                   onClick={() => window.open(`https://s5.sir.sportradar.com/betradar/en/match/${match.search_code}`, 'stats', 'width=1078,height=768')}
                                                   href="#" />
                                                <a className="head-grap"  href="#" />
                                            </div>
                                        </div>
                                    </td>
                                    {match.odds.length > 0 && match.odds.map((odd, index)=>
                                        index < 8 && (
                                            <td key={`prediction-${odd.sign_key}`}>
                                            <span
                                                onClick={() => selectOdd(odd, match)}
                                                className={`match-value odd quote ${isPredictionSelected(odd, match, activeLeague)?'selected': null}`}
                                            >{odd.rank}</span>
                                            </td>
                                            )
                                    )}
                                </tr>
                                <tr key={`c_${match.id}`}>
                                    <td colSpan="11">
                                        <div className={`SingleInsideStats_${match.search_code}`} />
                                    </td>
                                </tr>
                                </>
                            )}
                        </>
                    )}
                    </tbody>
                </table>
            </div>
        </div>
    )
}

const mapDispatchToProps = dispatch => ({
    addOddToSlip: odd => dispatch(addOddToSlip(odd))
});

const mapStateToProps = createStructuredSelector({
    rematchSports: selectRematchSportsBook,
    rematchLeagues: selectRematchLeagues,
    rematchGroups: selectRematchGroups,
    betSlip: selectBetSlipData,
});

export default connect(mapStateToProps, mapDispatchToProps)(TopBets);