import axios from "axios";
import Api from "./Api";

class AntePostSportsBook extends Api{

    fetchAntePostSports = async () => {
        return await axios.get(`${this.endPoint()}/sport-book/local/antepost/sports`, { withCredentials: true });
    }

    fetchAntePostGroupEvents = async (group_id) => {
        return await axios.get(`${this.endPoint()}/sport-book/antepost/events/${group_id}`, { withCredentials: true });
    }

    fetchAntePostEventOdds = async (search_code) => {
        return await axios.get(`${this.endPoint()}/sport-book/antepost/odds/${search_code}`, { withCredentials: true });
    }
}

export default new AntePostSportsBook;