import { createSelector } from "reselect";

const selectSpecialSports = state => state.special;

export const selectSpecialSportsBook = createSelector(
  [selectSpecialSports],
  sports => sports.specialSports
);

export const selectSpecialLeagues = createSelector(
    [selectSpecialSports],
    sports => sports.specialLeagues
);

export const selectSpecialGroups = createSelector(
    [selectSpecialSports],
    sports => sports.specialGroups
);


export const selectSpecialSportsBookFetchingStatus = createSelector(
    [selectSpecialSports],
    sports => sports.isFetching
);


export const selectSpecialSportsBookError = createSelector(
    [selectSpecialSports],
    sports => sports.errorMessage
);