import React, {useState, useEffect} from 'react';
import {addOddToSlip} from "../../Redux/custom/bets/bet-slip/bet-slip.actions";
import { connect } from "react-redux";
import {createStructuredSelector} from "reselect";
import {selectBetSlipData} from "../../Redux/custom/bets/bet-slip/bet-slip.selectors";

const EventFixturesList = ({ signs, activeGroup, leagueGroup, league, events, addOddToSlip, betSlip }) => {
    const [activeOdds, setActiveOdds] = useState([]);
    const [activeSign, setActiveSign] = useState([]);
    const [selectedItems, setSelectedItems] = useState([]);
    const [totalPredictions, setTotalPredictions] = useState([]);
    const [totalPredictionEvents, setTotalPredictionEvents] = useState([]);

    useEffect(() => {
        // Check the event odds that has the same signKey as the active sign sign key
        if (events){
            // console.log(betSlip)
        }
        setTotalPredictionEvents([]);
        calculatePredictionEvents(betSlip);
    }, [signs, events, betSlip]);

    const addOddToBestSlip = (betSlip, odd, event) => {

        // If league does not exist on the betslip
        let item = {
            id: `${leagueGroup.name}_${league.name}_${event.name}_${odd.sign_key}`,
            leagueGroup: leagueGroup.name,
            league: league.name,
            fixture: {
                event: event,
                prediction: odd
            }

        }
        // Check if the league exists
        addOddToSlip(item);
        return true;
    }

    const calculatePredictionEvents = (slip) => {
        var selected = [];
        slip.slip.forEach(league => {
            league.fixtures.forEach(fixture => {
                selected.push(fixture);
            });
        });

        setTotalPredictionEvents(selected);
    }

    const isPredictionSelected = (odd, event, league) => {
        var isPresent = false;
        betSlip.slip.forEach(selLeague => {
            if (selLeague.league === league.name){
                totalPredictionEvents?.forEach(preEvent => {
                    if (preEvent.name === event.name){
                        preEvent.predictions?.forEach(pre => {
                            if (pre.sign_key === odd.sign_key){
                                isPresent = true;
                            }
                        });
                    }
                });
            }
        });

        return isPresent;
    }

    const selectOdd = async (odd, event) => {
        addOddToBestSlip(betSlip, odd, event)
    }

    const setEventActiveOdds = (odds, event, league) => {
        let matchOdds = [];
        odds?.forEach(odd => {
            if (odd.group_name == activeGroup.group_name){
                matchOdds.push(odd);
            }
        });
        return matchOdds?.map((odd, index) =>
            index < 9 && (
                <li
                    onClick={() => selectOdd(odd, event)}
                    className={isPredictionSelected(odd, event, league)?'selected-prediction': null}
                >
                    <p>{odd.rank}</p>
                    <p className='sign-name'>{odd.sign_name}</p>
                </li>
            )
        )
    }

    return (
        <div className='fixtures-container'>
            <h5>Events</h5>
            <div className="custom-odds-table">
                <ul className='custom-signs' style={{ textAlign: "right" }}>
                    {signs?.map((sign, g) =>
                        <li
                            onClick={() => {
                                setActiveSign(sign);
                            }}
                        >
                        <span>{sign.sign_name}</span>
                        </li>
                    )}
                </ul>
            </div>
            {events?.map((event, index) =>
                <div className='custom-events-list'>
                    <div className='left-side-events-box'>
                        <span>{event.name}</span>
                        <p>{event.startdate}</p>
                    </div>
                    <div className='right-side-events-box'>
                        <ul>
                            {setEventActiveOdds(event.odds, event, league)}
                        </ul>
                    </div>

                </div>
            )}


        </div>
    )
}

const mapDispatchToProps = dispatch => ({
   addOddToSlip: odd => dispatch(addOddToSlip(odd))
});
const mapStateToProps = createStructuredSelector({
    betSlip: selectBetSlipData,
});
export default connect(mapStateToProps, mapDispatchToProps)(EventFixturesList);