import React from "react";
class Api{
    constructor() {
        if (process.env.NODE_ENV === 'development') {
            this.serverLink = 'http://127.0.0.1:8000'
        }else {
            this.serverLink = 'https://theoapi.telvida.com'
        }
    }

    endPoint = () => {
        return `${this.serverLink}/api`
    }

    userImagePath = (filename) => {
        return `${this.serverLink}/uploads/images/users/${filename}`
    }

    imagePath = (filename) => {
        return `${this.serverLink}/uploads/images/${filename}`
    }


    systemImagePath = (filename) => {
        return `${this.serverLink}/uploads/images/system/${filename}`
    }
}

export default Api;
