import { createSelector } from "reselect";

const selectRematchSports = state => state.rematch;

export const selectRematchSportsBook = createSelector(
  [selectRematchSports],
  sports => sports.rematchSports
);

export const selectRematchLeagues = createSelector(
    [selectRematchSports],
    sports => sports.rematchLeagues
);

export const selectRematchGroups = createSelector(
    [selectRematchSports],
    sports => sports.rematchGroups
);

export const selectRematchSportsBookFetchingStatus = createSelector(
    [selectRematchSports],
    sports => sports.isFetching
);


export const selectRematchSportsBookError = createSelector(
    [selectRematchSports],
    sports => sports.errorMessage
);